import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./userMnoCsraPortalService";

// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
// import Breadcrumb from "react-bootstrap/Breadcrumb";
// import { NavLink } from "react-router-dom";
// import FileBase64 from "react-file-base64";
import { getAll as getAllMnoCsraPortal } from "../mnoCsraPortal/mnoCsraPortalService";
import { getAll as getAllUser } from "../user/userService";

class UserMnoCsraPortalForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: [
      "USER_MNO_CSRA_PORTAL_CREATE",
      "USER_MNO_CSRA_PORTAL_UPDATE",
    ],
    moduleUrl: "/admin",
    basicUrl: "/admin/userMnoCsraPortals/",
    userPermissions: [],
    mnoCsraPortalOptions: [],
    userOptions: [],
    isLoading: true,
    data: { userId: "", mnoCsraPortalIds: "" },
    errors: {},
    validationMessage: {
      userId: "",
      mnoCsraPortal: "",
    },
    commonValidationMessage: "",
    translations: {
      // orgNameLabel: "Organization Name",
      titleLabel: "User Mno Csra Portal",
      userIdLabel: "User",
      userIdPlaceholder: "Enter User",
      mnoCsraPortalIdLabel: "MNO Csra Portal",
      mnoCsraPortalIdPlaceholder: "Select MNO Csra Portal",
    },
  };

  /* schema = {
    id: Joi.number().allow(null),
    userId: Joi.string().required().label({this.state.translations.userIdLabel}),mnoCsraPortal: Joi.number().label("Mno Csra Portal"),
  };
*/
  async populateData() {
    try {
      const thisContext = this;
      await getAllMnoCsraPortal().then(function (result) {
        let mnoCsraPortalOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          mnoCsraPortalOptions.push({
            value: result.data[i].id,
            label: result.data[i].mnoPortalTitle,
          });
        }
        thisContext.setState({ mnoCsraPortalOptions: mnoCsraPortalOptions });
      });

      await getAllUser().then(function (result) {
        let userOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          userOptions.push({
            value: result.data[i].id,
            label:
              result.data[i].firstName +
              " " +
              result.data[i].lastName +
              "-" +
              result.data[i].username,
          });
        }
        thisContext.setState({ userOptions: userOptions });
      });

      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
          this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          titleLabel: translationsObj["i18n.userMnoCsraPortal.titleLabel"]
          ? translationsObj["i18n.userMnoCsraPortal.titleLabel"]
          : thisContext.state.translations.titleLabel,

          userIdLabel: translationsObj["i18n.userMnoCsraPortal.userId.label"]
            ? translationsObj["i18n.userMnoCsraPortal.userId.label"]
            : thisContext.state.translations.userIdLabel,

          userIdPlaceholder: translationsObj[
            "i18n.userMnoCsraPortal.userId.placeholder"
          ]
            ? translationsObj["i18n.userMnoCsraPortal.userId.placeholder"]
            : thisContext.state.translations.userIdPlaceholder,
          mnoCsraPortalIdLabel: translationsObj[
            "i18n.userMnoCsraPortal.mnoCsraPortalId.label"
          ]
            ? translationsObj["i18n.userMnoCsraPortal.mnoCsraPortalId.label"]
            : thisContext.state.translations.mnoCsraPortalIdLabel,
          mnoCsraPortalIdPlaceholder: translationsObj[
            "i18n.userMnoCsraPortal.mnoCsraPortalId.placeholder"
          ]
            ? translationsObj[
                "i18n.userMnoCsraPortal.mnoCsraPortalId.placeholder"
              ]
            : thisContext.state.translations.mnoCsraPortalIdPlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });

    if (
      !(
        thisContext.state.userPermissions.includes(
          "USER_MNO_CSRA_PORTAL_CREATE"
        ) ||
        thisContext.state.userPermissions.includes(
          "USER_MNO_CSRA_PORTAL_UPDATE"
        )
      )
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      userId: obj.userId,
      mnoCsraPortalIds: obj.mnoCsraPortalIds,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    if (thisContext.state.data.mnoCsraPortalIds.length == 0) {
      this.state.validationMessage.mnoCsraPortal = "Please Select MNO Csra Portal.";
      return;
    }

    if (thisContext.state.data.userId==null) {
      this.state.validationMessage.userId = "Please Select User.";
      return;
    }

    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status == "FAILED" ||
        response.data.validationMessages != undefined
      ) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black">
                  <Card.Title className="card-form-title-dark">
                  {this.state.translations.titleLabel}
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello User Mno Csra Portal please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <div>
                      {this.renderSelect(
                        true,
                        false,
                        "userId",
                        this.state.translations.userIdLabel,
                        this.state.translations.userIdPlaceholder,
                        this.state.userOptions,
                        this.state.validationMessage.userId
                      )}
                      {this.renderSelect(
                        true,
                        true,
                        "mnoCsraPortalIds",
                        this.state.translations.mnoCsraPortalIdLabel,
                        this.state.translations.mnoCsraPortalIdPlaceholder,
                        this.state.mnoCsraPortalOptions,
                        this.state.validationMessage.mnoCsraPortal
                      )}

                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </div>
                  </Card.Body>
                  <Card.Footer>
                    <div className="form-group row">
                      <div className="text-left col-md-4">
                        {this.renderCancelButton("Cancel", function (e) {
                          e.preventDefault();
                          thisContext.props.history.push(
                            thisContext.state.basicUrl
                          );
                        })}
                      </div>
                      <div className="text-right col-md-4">
                        <span style={{ color: "red" }}>
                          {this.state.commonValidationMessage}
                        </span>
                      </div>
                      <div className="text-right col-md-4">
                        {this.renderButton("Save", this.handleSubmit)}
                      </div>
                    </div>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default UserMnoCsraPortalForm;
