import React, { Component } from "react";
import Pagination from "../common/pagination";
import { paginate } from "../common/paginate";
import { getAll, remove, save } from "./userRequestService";
import { checkAuthentication } from "../common/authenticationService";
import ListGroup from "../common/listGroup";
import Container from "react-bootstrap/Container";
import { Col, Row, Card, Form } from "react-bootstrap";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import { NavLink } from "react-router-dom";
// import userRequest from "./userRequest.json";
import UserRequestsTable from "./userRequestTable";
import _ from "lodash";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import UxCrudManager from "../common/UxCrudManager";

class userRequestManager extends UxCrudManager {
  state = {
    isLoading: true,
    requiredPermission: "USER_REQUEST_READ",
    categories: [
      "All UserRequests",
      "DRAFT",
      "APPROVED",
      "REJECTED",
      "DELETED",
    ],
    moduleUrl: "/admin",
    basicUrl: "/admin/userRequests/",
    titleLabel: "User Request",
    currentPage: 0,
    data: [],
    translations: {
      titleLabel: "User Request",
      homeLabel: "Home",
    },
    pageSize: 10,
    firstPage: true,
    lastPage: false,
    count: 0,
    totalElements: 0,
    currentItem: "All UserRequests",
    sortColumn: { path: "status", order: "asc" },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          homeLabel: translationsObj["i18n.common.homeLabel"]
            ? translationsObj["i18n.common.homeLabel"]
            : thisContext.state.translations.homeLabel,
          titleLabel: translationsObj["i18n.userRequest.titleLabel"]
            ? translationsObj["i18n.userRequest.titleLabel"]
            : thisContext.state.translations.titleLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadData = async () => {
    const thisContext = this;
    await checkAuthentication().then(function (result) {
      if (result) {
        // console.log("current page: " + thisContext.state.currentPage);
        // console.log("page size: " + thisContext.state.pageSize);
        getAll(thisContext.state.currentPage, thisContext.state.pageSize)
          .then((response) => {
            const { data: responseObj } = response;
            thisContext.setState({
              data: responseObj.content,
              pageSize: responseObj.pageable.pageSize,
              currentPage: responseObj.pageable.pageNumber,
              totalElements: responseObj.totalElements,
              firstPage: responseObj.first,
              lastPage: responseObj.last,
              isLoading: false,
            });
            // console.log(thisContext.state);
          })
          .catch((e) => {
            console.log("error occured!");
            console.log(e);
          });
      } else {
        thisContext.props.history.push("/login");
      }
    });
  };

  saveItem = async (item) => {
    await save(item);
  };

  deleteItem = async (item) => {
    await remove(item);
  };

  htmlContent() {
    let thisContext = this;
    const options = [
      { id: 0, value: "5" },
      { id: 1, value: "10" },
      { id: 2, value: "15" },
      { id: 3, value: "20" },
      { id: 4, value: "25" },
    ];
    const { columns, pageSize, currentPage, sortColumn, firstPage, lastPage } =
      this.state;

    const { totalCount, data } = this.getPagedData();
    return (
      <React.Fragment>
        {/* <DeleteConfirmModal /> */}
        <Row>
          {/*
           * <Col md="2" xs="2"> <ListGroup
           * currentItem={this.state.currentItem} textProperty="name"
           * valueProperty="_id" items={this.state.categories}
           * onItemSelect={this.handleCategorySelect} /> </Col>
           */}
          <Col md="12" xs="12">
            <Card className="bg-light text-black">
            <Card.Title className="card-form-title-dark">
                {this.state.translations.titleLabel}
              </Card.Title>
              <Card.Header>
              <Row>
                  <Col style={{ display: "flex", justifyContent: "left" }}>
                    <select
                      value={this.state.currentItem}
                      onChange={function (event) {
                        thisContext.handleCategorySelect(event.target.value);
                      }}
                    >
                      <option>-Select Status-</option>
                      {this.state.categories.map((category) => (
                        <option value={category} key={category}>
                          {category}
                        </option>
                      ))}
                    </select>{" "}
                  </Col>
                  <Col style={{ display: "flex", justifyContent: "right" }}>
                    {this.state.userPermissions.includes("MNO_CREATE") && (
                      <button
                        onClick={this.loadCreateForm}
                        className="btn btn-success"
                      >
                        <FontAwesomeIcon icon={faPlus} />
                      </button>
                    )}
                  </Col>
                </Row>
              </Card.Header>
              <Card.Body>
                <Row>
                  <Col></Col>
                  <Col></Col>
                  <Col>
                    <Form.Control
                      required
                      type="text"
                      as="select"
                      name="selectSize"
                    >
                      {options.map((option) => (
                        <option
                          key={option.id}
                          value={option.value}
                          onClick={() => this.handleSizeChange(option)}
                        >
                          {option.value}
                        </option>
                      ))}
                    </Form.Control>
                  </Col>
                </Row>

                {/* <Card.Title>User Request</Card.Title> */}
                {/* form>(div.form-group>label+input.form-control)*2 */}
                <UserRequestsTable
                  columns={columns}
                  sortColumn={sortColumn}
                  onSort={this.handleSort}
                  onLikeClick={this.handleChangeChk}
                  data={data}
                  onView={this.handleView}
                  onEdit={this.handleEdit}
                  onApprove={this.handleApprove}
                  onDelete={this.handleDelete}
                  onCreate={this.loadCreateForm}
                />
              </Card.Body>
              <Card.Footer>
                <Pagination
                  currentPage={currentPage}
                  itemsCount={totalCount}
                  pageSize={pageSize}
                  firstPage={firstPage}
                  lastPage={lastPage}
                  onPageChange={this.handlePageChange}
                />
              </Card.Footer>
            </Card>
          </Col>
        </Row>
      </React.Fragment>
    );
  }
}

export default userRequestManager;
