import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./customerReviewService";
// import Joi from "joi-browser";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';


class CustomerReviewForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["CUSTOMER_REVIEW_CREATE","CUSTOMER_REVIEW_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/customerReviews/",
	userPermissions: [],
    data: { id: null , customerName: "", customerImage: "", contactNumber: "", customerProfession: "", rating: "", reviewDetails: "", suggestions: "", name: "" },
    errors: {},
    validationMessage: {
      customerName: '',
customerImage: '',
contactNumber: '',
customerProfession: '',
rating: '',
reviewDetails: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Customer Review",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	customerNameLabel: "Customer Name",
customerNamePlaceholder: "Enter Customer Name",
customerImageLabel: "Customer Image",
customerImagePlaceholder: "Enter Customer Image",
contactNumberLabel: "Contact Number",
contactNumberPlaceholder: "Enter Contact Number",
customerProfessionLabel: "Customer Profession",
customerProfessionPlaceholder: "Enter Customer Profession",
ratingLabel: "Rating",
ratingPlaceholder: "Enter Rating",
reviewDetailsLabel: "Review Details",
reviewDetailsPlaceholder: "Enter Review Details",
suggestionsLabel: "Suggestions",
suggestionsPlaceholder: "Enter Suggestions",
nameLabel: "Name",
namePlaceholder: "Enter Name",

    },
	  
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    customerName: Joi.string().required().label({this.state.translations.customerNameLabel}),customerImage: Joi.string().required().label({this.state.translations.customerImageLabel}),contactNumber: Joi.string().required().label({this.state.translations.contactNumberLabel}),customerProfession: Joi.string().required().label({this.state.translations.customerProfessionLabel}),rating: Joi.string().required().label({this.state.translations.ratingLabel}),reviewDetails: Joi.string().required().label({this.state.translations.reviewDetailsLabel}),suggestions: Joi.string().label({this.state.translations.suggestionsLabel}),name: Joi.string().label({this.state.translations.nameLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      
      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.customerReview.titleLabel"] ? translationsObj["i18n.customerReview.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          customerNameLabel: translationsObj["i18n.customerReview.customerName.label"] ? translationsObj["i18n.customerReview.customerName.label"] : thisContext.state.translations.customerNameLabel,
customerNamePlaceholder: translationsObj["i18n.customerReview.customerName.placeholder"] ? translationsObj["i18n.customerReview.customerName.placeholder"] : thisContext.state.translations.customerNamePlaceholder,
customerImageLabel: translationsObj["i18n.customerReview.customerImage.label"] ? translationsObj["i18n.customerReview.customerImage.label"] : thisContext.state.translations.customerImageLabel,
customerImagePlaceholder: translationsObj["i18n.customerReview.customerImage.placeholder"] ? translationsObj["i18n.customerReview.customerImage.placeholder"] : thisContext.state.translations.customerImagePlaceholder,
contactNumberLabel: translationsObj["i18n.customerReview.contactNumber.label"] ? translationsObj["i18n.customerReview.contactNumber.label"] : thisContext.state.translations.contactNumberLabel,
contactNumberPlaceholder: translationsObj["i18n.customerReview.contactNumber.placeholder"] ? translationsObj["i18n.customerReview.contactNumber.placeholder"] : thisContext.state.translations.contactNumberPlaceholder,
customerProfessionLabel: translationsObj["i18n.customerReview.customerProfession.label"] ? translationsObj["i18n.customerReview.customerProfession.label"] : thisContext.state.translations.customerProfessionLabel,
customerProfessionPlaceholder: translationsObj["i18n.customerReview.customerProfession.placeholder"] ? translationsObj["i18n.customerReview.customerProfession.placeholder"] : thisContext.state.translations.customerProfessionPlaceholder,
ratingLabel: translationsObj["i18n.customerReview.rating.label"] ? translationsObj["i18n.customerReview.rating.label"] : thisContext.state.translations.ratingLabel,
ratingPlaceholder: translationsObj["i18n.customerReview.rating.placeholder"] ? translationsObj["i18n.customerReview.rating.placeholder"] : thisContext.state.translations.ratingPlaceholder,
reviewDetailsLabel: translationsObj["i18n.customerReview.reviewDetails.label"] ? translationsObj["i18n.customerReview.reviewDetails.label"] : thisContext.state.translations.reviewDetailsLabel,
reviewDetailsPlaceholder: translationsObj["i18n.customerReview.reviewDetails.placeholder"] ? translationsObj["i18n.customerReview.reviewDetails.placeholder"] : thisContext.state.translations.reviewDetailsPlaceholder,
suggestionsLabel: translationsObj["i18n.customerReview.suggestions.label"] ? translationsObj["i18n.customerReview.suggestions.label"] : thisContext.state.translations.suggestionsLabel,
suggestionsPlaceholder: translationsObj["i18n.customerReview.suggestions.placeholder"] ? translationsObj["i18n.customerReview.suggestions.placeholder"] : thisContext.state.translations.suggestionsPlaceholder,
nameLabel: translationsObj["i18n.customerReview.name.label"] ? translationsObj["i18n.customerReview.name.label"] : thisContext.state.translations.nameLabel,
namePlaceholder: translationsObj["i18n.customerReview.name.placeholder"] ? translationsObj["i18n.customerReview.name.placeholder"] : thisContext.state.translations.namePlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("CUSTOMER_REVIEW_CREATE") ||
      thisContext.state.userPermissions.includes("CUSTOMER_REVIEW_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      customerName: obj.customerName,customerImage: obj.customerImage,contactNumber: obj.contactNumber,customerProfession: obj.customerProfession,rating: obj.rating,reviewDetails: obj.reviewDetails,suggestions: obj.suggestions,name: obj.name,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

  
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-dark">{this.state.translations.titleLabel}</Card.Title>
			{/*<Card.Header>
				<p>Hello Customer Review please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}
				<div>
                {this.renderInput(false,false,"id", "Id", "hidden")}
                {this.renderInput(true,true,"customerName", this.state.translations.customerNameLabel,this.state.translations.customerNamePlaceholder  ,"text",false,this.state.validationMessage.customerName)}
<div className="row">
                        <div className="col-md-2">
                          <b><label htmlFor="customerImage">{this.state.translations.customerImageLabel}</label></b>
                        </div>
                        <div className="col-md-6">
                          <FileBase64
                            multiple={false}
                            onDone={(file) => {
                              let data = thisContext.state.data;
                              data.customerImage = file.base64;
                              thisContext.setState({ data: data });
                            }
                            } />

                        </div>
    <div className="col-md-1">
                          <img width={'120px'} height={'100px'} src={this.state.data.customerImage} alt="Customer Image" />
                          <span style={{ color: 'red' }}>*</span>
                        </div>
      <div className="col-md-3">
								</div>					 </div>
{this.renderInput(true,true,"contactNumber", this.state.translations.contactNumberLabel,this.state.translations.contactNumberPlaceholder  ,"text",false,this.state.validationMessage.contactNumber)}
{this.renderInput(true,true,"customerProfession", this.state.translations.customerProfessionLabel,this.state.translations.customerProfessionPlaceholder  ,"text",false,this.state.validationMessage.customerProfession)}
{this.renderInput(true,true,"rating", this.state.translations.ratingLabel,this.state.translations.ratingPlaceholder  ,"number",false,this.state.validationMessage.rating)}
{this.renderInput(true,true,"reviewDetails", this.state.translations.reviewDetailsLabel,this.state.translations.reviewDetailsPlaceholder  ,"textarea",false,this.state.validationMessage.reviewDetails)}
{this.renderInput(false,true,"suggestions", this.state.translations.suggestionsLabel,this.state.translations.suggestionsPlaceholder  ,"textarea",false,this.state.validationMessage.suggestions)}
{this.renderInput(false,true,"name", this.state.translations.nameLabel,this.state.translations.namePlaceholder  ,"text",false,this.state.validationMessage.name)}

                {/* {this.renderInput("dueDate", "Due Date")} */}
				</div>
                
            </Card.Body>
			<Card.Footer>
			  <div className="form-group row">
	                <div className="text-left col-md-4">
		                {
		            		this.renderCancelButton(thisContext.state.translations.cancelLabel,function(e){
		            			e.preventDefault();
		            			thisContext.props.history.push(thisContext.state.basicUrl);
		            		})
		            	}
	                </div>
					<div className="text-right col-md-4">
	                    <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
	                </div>
	                <div className="text-right col-md-4">
	                		{this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
	            	</div>
                
               </div>
			</Card.Footer>
          </Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default CustomerReviewForm;
