import React, { useState, useEffect } from "react";
import axios from "axios";
import { Table, Spinner } from "react-bootstrap";


import {
  getAllApplicationItemStatuss,
  getAllDailyApplicationItems,
} from "../common/dashboardService";

const AppStatusData = ({ userPermissions }) => {
  // State variables to store inventory data
  const [totalProducts, setTotalProducts] = useState(0);
  const [totalStock, setTotalStock] = useState(0);
  const [totalSales, setTotalSales] = useState(0);
  const [totalOrders, setTotalOrders] = useState(0);
  const [dailyApplicationItems, setDailyApplicationItems] = useState([]);
  const [applicationItemStatuss, setApplicationItemStatuss] = useState([]);
  const [isLoading, setIsLoading] = useState([]);

  // Function to fetch dashboard data from the server
  const fetchDailyApplicationItems = async () => {
    try {
      setIsLoading(true);
      await getAllDailyApplicationItems().then(function (response) {
        const fetchObject = response.data;
        setDailyApplicationItems(fetchObject);
        setIsLoading(false);
      });
    } catch (error) {
      console.error("Error fetching dashboard data:", error);
    }
  };

  // Function to fetch dashboard data from the server
  const fetchApplicationItemStatus = async () => {
    try {
      setIsLoading(true);
      await getAllApplicationItemStatuss().then(function (response) {
        const fetchObject = response.data;
        setApplicationItemStatuss(fetchObject);
        setIsLoading(false);
      });
    } catch (error) {
      console.error("Error fetching dashboard data:", error);
    }
  };

  // Fetch dashboard data when the component mounts
  useEffect(() => {
    fetchDailyApplicationItems();
    fetchApplicationItemStatus();
  }, []);
  const createKey = (item, column) => {
    return item.id + (column.path || column.key);
  };

  if (isLoading) {
    return (
      <Spinner animation="border" role="status">
        <span className="visually-hidden"></span>
      </Spinner>
    );
  }

  return (
    <>
      {userPermissions.includes("APP_STATUS_READ") && (
        <div>
          <h5>Total Data Count</h5>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Data Type</th>
                <th>Total</th>
                <th>Draft</th>
                {/* <th>Approved</th> */}
                <th>Rejected</th>
                <th>Deleted</th>
              </tr>
            </thead>

            <tbody>
              {applicationItemStatuss.map((item) => (
                <tr key={item.id}>
                  <td>{item.itemName}</td>
                  <td>{item.total}</td>
                  <td>{item.totalDraftItem}</td>
                  {/* <td>{item.totalApprovedItem}</td> */}
                  <td>{item.totalRejectedItem}</td>
                  <td>{item.totalDeletedItem}</td>
                </tr>
              ))}
            </tbody>
          </Table>
          <h5>Application Count Info</h5>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Total Users</th>
                <th>Total Roles</th>
                <th>Total Operators</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{totalProducts}</td>
                <td>{totalStock}</td>
                <td>{totalSales}</td>
              </tr>
            </tbody>
          </Table>

          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Last 10 Transactions</th>
                <th>Last 10 Users</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>No Transactions Yet</td>
                <td>No Users Yet</td>
              </tr>
            </tbody>
          </Table>

          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Notifications</th>
                <th>Sender</th>
                <th>Date</th>
                <th>Is Checked?</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>No Notifications yet received</td>
                <td>N/A</td>
                <td>N/A</td>
                <td>N/A</td>
              </tr>
            </tbody>
          </Table>
        </div>
      )}
    </>
  );
};

export default AppStatusData;
