import http from "./httpService";
import config from "../../config.json";
import axios from "axios";

// import { reject } from "lodash";

const registrationEndPoint = config.apiUrl +  "/public/registration";
const registerVerifyEndPoint = config.apiUrl +  "/public/registration_verify";
const options = {
  headers: { Authorization: "Bearer " + localStorage.getItem("token") },
};

export function createNew(userRequest) {
  userRequest.id = null;
  return http.post(registrationEndPoint, userRequest);
}

export function verifyRegistration(email, registrationCode) {
  return http.get(registerVerifyEndPoint + "?email=" + email + "&registrationCode=" + registrationCode);
}