import React, { Component } from "react";
import Table from "../common/table";
import { Button, Form, Modal, FormGroup, FormControl } from 'react-bootstrap';
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faPencilAlt,
  faArrowRight
} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class UserRequestViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" size="sm" onClick={this.props.onClick}>
          <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form>
              <Form.Group controlId="email">
                <Form.Label>Email</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.email} placeholder="Email" />
              </Form.Group>
              <Form.Group controlId="password">
                <Form.Label>Password</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.password} placeholder="Password" />
              </Form.Group>
              <Form.Group controlId="firstName">
                <Form.Label>First Name</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.firstName} placeholder="First Name" />
              </Form.Group>
              <Form.Group controlId="lastName">
                <Form.Label>Last Name</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.lastName} placeholder="Last Name" />
              </Form.Group>
              <Form.Group controlId="requestedRole">
                <Form.Label>Requested Role</Form.Label>
                <Form.Control disabled="true" type="text" value={this.props.data.requestedRole} placeholder="Requested Role" />
              </Form.Group>


              {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
            </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class UserRequestTable extends UxCrudTable {
  state = {
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    title: "User Request Details",
    translations: {
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      titleLabel: "User Request",
      emailLabel: "Email",
      firstNameLabel: "First Name",
      lastNameLabel: "Last Name",
      requestedRoleLabel: "Requested Role",
    },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,

          homeLabel: translationsObj["i18n.common.homeLabel"]
            ? translationsObj["i18n.common.homeLabel"]
            : thisContext.state.translations.homeLabel,
          titleLabel: translationsObj["i18n.userRequest.titleLabel"]
            ? translationsObj["i18n.userRequest.titleLabel"]
            : thisContext.state.translations.titleLabel,
          emailLabel: translationsObj["i18n.userRequest.email.label"]
            ? translationsObj["i18n.userRequest.email.label"]
            : thisContext.state.translations.emailLabel,

          firstNameLabel: translationsObj["i18n.userRequest.firstName.label"]
            ? translationsObj["i18n.userRequest.firstName.label"]
            : thisContext.state.translations.firstNameLabel,

          lastNameLabel: translationsObj["i18n.userRequest.lastName.label"]
            ? translationsObj["i18n.userRequest.lastName.label"]
            : thisContext.state.translations.lastNameLabel,

          requestedRoleLabel: translationsObj["i18n.userRequest.requestedRole.label"]
            ? translationsObj["i18n.userRequest.requestedRole.label"]
            : thisContext.state.translations.requestedRoleLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }
  async loadColumns() {
    let columns = [
      { path: "id", label: "Id" },
      {
        path: "email",
        label: this.state.translations.emailLabel,
      }
      , {
        path: "firstName",
        label: this.state.translations.firstNameLabel,
      }, {
        path: "lastName",
        label: this.state.translations.lastNameLabel,
      }, {
        path: "requestedRole",
        label: this.state.translations.requestedRoleLabel,
      },
      {
        key: "view",
        label: this.state.translations.viewLabel,
        content: (item) => (
          <UserRequestViewModal
            itemId={this.state.itemId}
            show={this.state.showView}
            title={this.state.title}
            body={this.state.body}
            data={this.state.data}
            onClick={() => this.handleViewShow(item)}
            onHide={this.handleViewClose}
          />
        ),
      },
      {
        key: "edit",
        label: this.state.translations.editLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onEdit(item)}
            className="btn btn-sm btn-info"
          >
            <FontAwesomeIcon icon={faPencilAlt} />
          </button>
        ),
      },
      {
        key: "approve",
        label: this.state.translations.approveLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onApprove(item)}
            className="btn btn-sm btn-success"
            disabled={item.status == 'APPROVED'}
          >
            <FontAwesomeIcon icon={faArrowRight} />
          </button>
        ),
      },
      {
        key: "delete",
        label: this.state.translations.deleteLabel,
        content: (item) => (
          <DeleteConfirmModal
            disabled={item.status == 'APPROVED'}
            show={this.state.showDeleteConfirm}
            item={this.state.item}
            onClick={() => this.handleDeleteModalShow(item)}
            onHide={this.handleDeleteConfirmClose}
            onConfirm={(item) => this.handleDeleteFromModal(item)}
          />
        ),
      },
    ];
    this.setState({ columns: columns });
  }


}

export default UserRequestTable;
