import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./mnoCsraAuthorizationKeyService";
// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import { getAll as getAllMno } from "../mno/mnoService";

class MnoCsraAuthorizationKeyForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["MNO_CSRA_AUTHORIZATION_KEY_CREATE","MNO_CSRA_AUTHORIZATION_KEY_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/mnoCsraAuthorizationKeys/",
    userPermissions: [],
    data: {
      id: null,
      mnoId: "",
      keyCollectionDate: "",
      authorizationKey: "",
      expireDate: "",
      remarks: "",
    },
    errors: {},
    validationMessage: {
      mno: "",
      keyCollectionDate: "",
      authorizationKey: "",
      authorizationValue: "",
      expireDate: "",
    },
    commonValidationMessage: "",
    translations: {
      titleLabel: "Mno Csra Authorization Key",
      mnoIdLabel: "MNO",
      mnoIdPlaceholder: "Select MNO",
      keyCollectionDateLabel: "Key Collection Date",
      keyCollectionDatePlaceholder: "Enter Key Collection Date",
      authorizationKeyLabel: "Authorization Key",
      authorizationKeyPlaceholder: "Enter Authorization Key",
      authorizationValueLabel: "Authorization Value",
      authorizationValuePlaceholder: "Enter Authorization Value",
      expireDateLabel: "Expire Date",
      expireDatePlaceholder: "Enter Expire Date",
      remarksLabel: "Remarks",
      remarksPlaceholder: "Enter Remarks",
    },
  };
  mnoOptions = [];

  /* schema = {
    id: Joi.number().allow(null),
    mno: Joi.number().label("Mno"),keyCollectionDate: Joi.string().required().label({this.state.translations.keyCollectionDateLabel}),authorizationKey: Joi.string().required().label({this.state.translations.authorizationKeyLabel}),expireDate: Joi.string().required().label({this.state.translations.expireDateLabel}),remarks: Joi.string().label({this.state.translations.remarksLabel}),
  };
*/
  async populateData() {
    try {
      const thisContext = this;
      await getAllMno().then(function (result) {
        for (var i = 0; i < result.data.length; i++) {
          thisContext.mnoOptions.push({
            value: result.data[i].id,
            label: result.data[i].description,
          });
        }
      });
      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
          this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          titleLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.titleLabel"
          ]
            ? translationsObj["i18n.mnoCsraAuthorizationKey.titleLabel"]
            : thisContext.state.translations.titleLabel,

            mnoIdLabel: translationsObj[
              "i18n.mnoCsraAuthorizationKey.mnoId.label"
            ]
              ? translationsObj["i18n.mnoCsraAuthorizationKey.mnoId.label"]
              : thisContext.state.translations.mnoIdLabel,
            
          mnoIdPlaceholder: translationsObj[
            "i18n.mnoCsraAuthorizationKey.mnoId.placeholder"
          ]
            ? translationsObj["i18n.mnoCsraAuthorizationKey.mnoId.placeholder"]
            : thisContext.state.translations.mnoIdPlaceholder,
          keyCollectionDateLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.keyCollectionDate.label"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.keyCollectionDate.label"
              ]
            : thisContext.state.translations.keyCollectionDateLabel,
          keyCollectionDatePlaceholder: translationsObj[
            "i18n.mnoCsraAuthorizationKey.keyCollectionDate.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.keyCollectionDate.placeholder"
              ]
            : thisContext.state.translations.keyCollectionDatePlaceholder,

          authorizationKeyLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.authorizationKey.label"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.authorizationKey.label"
              ]
            : thisContext.state.translations.authorizationKeyLabel,
          authorizationKeyPlaceholder: translationsObj[
            "i18n.mnoCsraAuthorizationKey.authorizationKey.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.authorizationKey.placeholder"
              ]
            : thisContext.state.translations.authorizationKeyPlaceholder,


          authorizationValueLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.authorizationValue.label"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.authorizationValue.label"
              ]
            : thisContext.state.translations.authorizationValueLabel,
          authorizationValuePlaceholder: translationsObj[
            "i18n.mnoCsraAuthorizationKey.authorizationValue.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.authorizationValue.placeholder"
              ]
            : thisContext.state.translations.authorizationValuePlaceholder,


          expireDateLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.expireDate.label"
          ]
            ? translationsObj["i18n.mnoCsraAuthorizationKey.expireDate.label"]
            : thisContext.state.translations.expireDateLabel,
          expireDatePlaceholder: translationsObj[
            "i18n.mnoCsraAuthorizationKey.expireDate.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.expireDate.placeholder"
              ]
            : thisContext.state.translations.expireDatePlaceholder,
          remarksLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.remarks.label"
          ]
            ? translationsObj["i18n.mnoCsraAuthorizationKey.remarks.label"]
            : thisContext.state.translations.remarksLabel,
          remarksPlaceholder: translationsObj[
            "i18n.mnoCsraAuthorizationKey.remarks.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.remarks.placeholder"
              ]
            : thisContext.state.translations.remarksPlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(
        thisContext.state.userPermissions.includes(
          "MNO_CSRA_AUTHORIZATION_KEY_CREATE"
        ) ||
        thisContext.state.userPermissions.includes(
          "MNO_CSRA_AUTHORIZATION_KEY_UPDATE"
        )
      )
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      mnoId: obj.mnoId,
      keyCollectionDate: obj.keyCollectionDate,
      authorizationKey: obj.authorizationKey,
      authorizationValue: obj.authorizationValue,
      expireDate: obj.expireDate,
      remarks: obj.remarks,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status == "FAILED" ||
        response.data.validationMessages != undefined
      ) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    const { data, errors } = this.state;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black">
                  <Card.Title className="card-form-title-dark">
                    {this.state.translations.titleLabel}
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello Mno Csra Authorization Key please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <div>
                      {this.renderInput(false, false, "id", "Id","", "hidden")}
                      {this.renderSelect(
                        true,
                        false,
                        "mnoId",
                        this.state.translations.mnoIdLabel,
                        this.state.translations.mnoIdPlaceholder,
                        thisContext.mnoOptions,
                        this.state.validationMessage.mnoId
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "keyCollectionDate",
                        this.state.translations.keyCollectionDateLabel,
                        this.state.translations.keyCollectionDatePlaceholder,
                        "date",
                        false,
                        this.state.validationMessage.keyCollectionDate
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "authorizationKey",
                        this.state.translations.authorizationKeyLabel,
                        this.state.translations.authorizationKeyPlaceholder,
                        "text",
                        false,
                        this.state.validationMessage.authorizationKey
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "authorizationValue",
                        this.state.translations.authorizationValueLabel,
                        this.state.translations.authorizationValuePlaceholder,
                        "textarea",
                        false,
                        this.state.validationMessage.authorizationValue
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "expireDate",
                        this.state.translations.expireDateLabel,
                        this.state.translations.expireDatePlaceholder,
                        "date",
                        false,
                        this.state.validationMessage.expireDate
                      )}
                      {this.renderInput(
                        false,
                        true,
                        "remarks",
                        this.state.translations.remarksLabel,
                        this.state.translations.remarksPlaceholder,
                        "textarea",
                        false,
                        this.state.validationMessage.remarks
                      )}

                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </div>
                  </Card.Body>
                  <Card.Footer>
                    <div className="form-group row">
                      <div className="text-left col-md-4">
                        {this.renderCancelButton("Cancel", function (e) {
                          e.preventDefault();
                          thisContext.props.history.push(
                            "/admin/mnoCsraAuthorizationKeys"
                          );
                        })}
                      </div>
                      <div className="text-right col-md-4">
                        <span style={{ color: "red" }}>
                          {thisContext.state.commonValidationMessage}
                        </span>
                      </div>
                      <div className="text-right col-md-4">
                        {this.renderButton("Save", this.handleSubmit)}
                      </div>
                    </div>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default MnoCsraAuthorizationKeyForm;
