import React, { Component } from "react";
import Pagination from "../common/pagination";
import { paginate } from "../common/paginate";
import { get, getAllPaginated, remove, save } from "./translationsService";
import { checkAuthentication } from "../common/authenticationService";
// import ListGroup from "../common/listGroup";
import Container from "react-bootstrap/Container";
import { Col, Row, Card } from "react-bootstrap";
// import translations from "./translations.json";
import TranslationssTable from "./translationsTable";
import _ from "lodash";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import UxCrudManager from "../common/UxCrudManager";

class TranslationsManager extends UxCrudManager {
  state = {
    requiredPermission: "TRANSLATIONS_READ",
    basicUrl: "/admin/translationss/",
    catagorySelectionLabel: "All Translations",
    categories: [
      "All Translationss",
      "DRAFT",
      "APPROVED",
      "REJECTED",
      "DELETED",
    ],
    titleLabel: "Translations",
    userPermissions: [],
    data: [],
    currentPage: 0,
    translations: { titleLabel: "Translations" },
    pageSize: 10,
    firstPage: true,
    lastPage: false,
    count: 0,
    currentItem: "All Translationss",
    sortColumn: { path: "status", order: "asc" },
    totalElements: 0,
  };

  loadData = async () => {
    let thisContext = this;
    thisContext.setState({ isLoading: true });
    await checkAuthentication().then(function (result) {
      if (result) {
        // console.log("current page: " + thisContext.state.currentPage);
        // console.log("page size: " + thisContext.state.pageSize);
        getAllPaginated(
          thisContext.state.currentPage,
          thisContext.state.pageSize
        )
          .then((response) => {
            const { data: responseObj } = response;
            thisContext.setState({
              data: responseObj.content,
              pageSize: responseObj.pageable.pageSize,
              currentPage: responseObj.pageable.pageNumber,
              totalElements: responseObj.totalElements,
              firstPage: responseObj.first,
              lastPage: responseObj.last,
              isLoading: false,
            });
            thisContext.getPagedData();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        thisContext.props.history.push("/login");
      }
    });
  };

  saveItem = async (item) => {
    await save(item);
  };

  deleteItem = async (item) => {
    await remove(item);
  };

  htmlContent() {
    let thisContext = this;
    const { columns, pageSize, currentPage, sortColumn, firstPage, lastPage } =
      this.state;
    const { totalCount, data } = this.getPagedData();
    return (
      <React.Fragment>
        {/* <DeleteConfirmModal /> */}
        <Container className="bg-light text-black">
          <Row>
            {/*
             * <Col md="2" xs="2"> <ListGroup
             * currentItem={this.state.currentItem} textProperty="name"
             * valueProperty="_id" items={this.state.categories}
             * onItemSelect={this.handleCategorySelect} /> </Col>
             */}
            <Col md="12" xs="12">
              <Card className="bg-light text-black">
                <Card.Title className="card-form-title-dark">
                  {this.state.titleLabel}
                </Card.Title>
                <Card.Header>
                  <Row>
                    <Col style={{ display: "flex", justifyContent: "left" }}>
                      <select
                        value={this.state.currentItem}
                        onChange={function (event) {
                          thisContext.handleCategorySelect(event.target.value);
                        }}
                      >
                        <option>-Select Status-</option>
                        {this.state.categories.map((category) => (
                          <option value={category} key={category}>
                            {category}
                          </option>
                        ))}
                      </select>{" "}
                    </Col>
                    <Col style={{ display: "flex", justifyContent: "right" }}>
                      {this.state.userPermissions.includes("USER_CREATE") && (
                        <button
                          onClick={this.loadCreateForm}
                          className="btn btn-sm btn-success"
                        >
                          <FontAwesomeIcon icon={faPlus} />
                        </button>
                      )}
                    </Col>
                  </Row>
                </Card.Header>
                <Card.Body>
                  {/* <Card.Title>User Request</Card.Title> */}
                  {/* form>(div.form-group>label+input.form-control)*2 */}
                  <TranslationssTable
                    columns={columns}
                    sortColumn={sortColumn}
                    onSort={this.handleSort}
                    onLikeClick={this.handleChangeChk}
                    data={data}
                    onView={this.handleView}
                    onEdit={this.handleEdit}
                    onApprove={this.handleApprove}
                    onDelete={this.handleDelete}
                    onCreate={this.loadCreateForm}
                    stateValues={this.props.stateValues}
                  />
                </Card.Body>
                <Card.Footer>
                  <Pagination
                    currentPage={currentPage}
                    itemsCount={totalCount}
                    pageSize={pageSize}
                    firstPage={firstPage}
                    lastPage={lastPage}
                    onPageChange={this.handlePageChange}
                  />
                </Card.Footer>
              </Card>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default TranslationsManager;
