import http from "../common/httpService";
import config from "../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/productPrices";

function productPriceUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}
export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(productPriceId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(productPriceUrl(productPriceId), options);
}


export function save(datObj) {
	return datObj.name != null ? update(datObj) : create(datObj);
}


export function create(productPrice) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, productPrice, options);
}

export function update(productPrice) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, productPrice, options);
}

export function remove(productPriceId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(productPriceUrl(productPriceId), options);
}

