import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./subCategoryService";
// import Joi from "joi-browser";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';
import { getAll as getAllCategory } from "../category/categoryService";

class SubCategoryForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["SUB_CATEGORY_CREATE","SUB_CATEGORY_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/subCategorys/",
	userPermissions: [],
    data: { id: null , categoryId: "", name: "", description: "" },
    errors: {},
    validationMessage: {
      category: '',
name: '',
description: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Sub Category",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	categoryIdLabel: "Category",
categoryIdPlaceholder: "Select Category",
nameLabel: "Name",
namePlaceholder: "Enter Name",
descriptionLabel: "Description",
descriptionPlaceholder: "Enter Description",

    },
	  categoryOptions : [],
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    category: Joi.number().label("Category"),name: Joi.string().required().label({this.state.translations.nameLabel}),description: Joi.string().required().label({this.state.translations.descriptionLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      await getAllCategory().then(function (result) {
        let categoryOptions = [];
        for (var i = 0; i < result.data.length; i++) {
          categoryOptions.push({
            value: result.data[i].id,
            label: result.data[i].name,
          });
        }
        thisContext.setState({ categoryOptions: categoryOptions });
      });

      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.subCategory.titleLabel"] ? translationsObj["i18n.subCategory.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          categoryIdLabel: translationsObj["i18n.subCategory.categoryId.label"] ? translationsObj["i18n.subCategory.categoryId.label"] : thisContext.state.translations.categoryIdLabel,
categoryIdPlaceholder: translationsObj["i18n.subCategory.categoryId.placeholder"] ? translationsObj["i18n.subCategory.categoryId.placeholder"] : thisContext.state.translations.categoryIdPlaceholder,
nameLabel: translationsObj["i18n.subCategory.name.label"] ? translationsObj["i18n.subCategory.name.label"] : thisContext.state.translations.nameLabel,
namePlaceholder: translationsObj["i18n.subCategory.name.placeholder"] ? translationsObj["i18n.subCategory.name.placeholder"] : thisContext.state.translations.namePlaceholder,
descriptionLabel: translationsObj["i18n.subCategory.description.label"] ? translationsObj["i18n.subCategory.description.label"] : thisContext.state.translations.descriptionLabel,
descriptionPlaceholder: translationsObj["i18n.subCategory.description.placeholder"] ? translationsObj["i18n.subCategory.description.placeholder"] : thisContext.state.translations.descriptionPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("SUB_CATEGORY_CREATE") ||
      thisContext.state.userPermissions.includes("SUB_CATEGORY_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      categoryId: obj.categoryId,name: obj.name,description: obj.description,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

  
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-dark">{this.state.translations.titleLabel}</Card.Title>
			{/*<Card.Header>
				<p>Hello Sub Category please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}
				<div>
                {this.renderInput(false,false,"id", "Id", "hidden")}
                {this.renderSelect(true,false,"categoryId", this.state.translations.categoryIdLabel, this.state.translations.categoryIdPlaceholder, thisContext.state.categoryOptions, this.state.validationMessage.categoryId)}
{this.renderInput(true,true,"name", this.state.translations.nameLabel,this.state.translations.namePlaceholder  ,"text",false,this.state.validationMessage.name)}
{this.renderInput(true,true,"description", this.state.translations.descriptionLabel,this.state.translations.descriptionPlaceholder  ,"textarea",false,this.state.validationMessage.description)}

                {/* {this.renderInput("dueDate", "Due Date")} */}
				</div>
                
            </Card.Body>
			<Card.Footer>
			  <div className="form-group row">
	                <div className="text-left col-md-4">
		                {
		            		this.renderCancelButton(thisContext.state.translations.cancelLabel,function(e){
		            			e.preventDefault();
		            			thisContext.props.history.push(thisContext.state.basicUrl);
		            		})
		            	}
	                </div>
					<div className="text-right col-md-4">
	                    <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
	                </div>
	                <div className="text-right col-md-4">
	                		{this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
	            	</div>
                
               </div>
			</Card.Footer>
          </Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default SubCategoryForm;
