import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./permissionGroupService";
import { getAll as getAllAppPermission } from "../appPermission/appPermissionService";

// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import { NavLink } from "react-router-dom";
import FileBase64 from "react-file-base64";

class PermissionGroupForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["PERMISSION_GROUP_CREATE","PERMISSION_GROUP_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/permissionGroups/",
    userPermissions: [],
    data: { id: null, groupName: "", description: "", permissions : [] },
    errors: {},
    validationMessage: {
      groupName: "",
      description: "",
      permissions: "",
    },
    commonValidationMessage: "",
    translations: {
      // orgNameLabel: "Organization Name",
      titleLabel: "Permission Group",
      groupNameLabel: "Permission Group",
      groupNamePlaceholder: "Enter Permission Group",
      descriptionLabel: "Description",
      descriptionPlaceholder: "Enter Description",
      permissionsLabel: "Permissions",
      permissionsPlaceholder: "Select Permissions",
    },
  };
  appPermissionOptions = [];

  /* schema = {
    id: Joi.number().allow(null),
    groupName: Joi.string().required().label({this.state.translations.groupNameLabel}),description: Joi.string().required().label({this.state.translations.descriptionLabel}),
  };
*/
  async populateData() {
    try {
      const thisContext = this;
      await getAllAppPermission().then(function (result) {
        for (var i = 0; i < result.data.length; i++) {
          thisContext.appPermissionOptions.push({
            value: result.data[i].id,
            label: result.data[i].permissionName,
          });
        }
      });

      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
          this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          titleLabel: translationsObj["i18n.permissionGroup.titleLabel"]
          ? translationsObj["i18n.permissionGroup.groupName.label"]
          : thisContext.state.translations.titleLabel,

          groupNameLabel: translationsObj["i18n.permissionGroup.groupName.label"]
            ? translationsObj["i18n.permissionGroup.groupName.label"]
            : thisContext.state.translations.groupNameLabel,
          groupNamePlaceholder: translationsObj[
            "i18n.permissionGroup.groupName.placeholder"
          ]
            ? translationsObj["i18n.permissionGroup.groupName.placeholder"]
            : thisContext.state.translations.groupNamePlaceholder,
          descriptionLabel: translationsObj[
            "i18n.permissionGroup.description.label"
          ]
            ? translationsObj["i18n.permissionGroup.description.label"]
            : thisContext.state.translations.descriptionLabel,
            descriptionPlaceholder: translationsObj[
              "i18n.permissionGroup.description.placeholder"
            ]
              ? translationsObj["i18n.permissionGroup.description.placeholder"]
              : thisContext.state.translations.descriptionPlaceholder,

              permissionsLabel: translationsObj[
                "i18n.permissionGroup.permissions.label"
              ]
                ? translationsObj["i18n.permissionGroup.permissions.label"]
                : thisContext.state.translations.permissionsLabel,

              permissionsPlaceholder: translationsObj[
                "i18n.permissionGroup.permissions.placeholder"
              ]
                ? translationsObj["i18n.permissionGroup.permissions.placeholder"]
                : thisContext.state.translations.permissionsPlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(
        thisContext.state.userPermissions.includes("PERMISSION_GROUP_CREATE") ||
        thisContext.state.userPermissions.includes("PERMISSION_GROUP_UPDATE")
      )
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      groupName: obj.groupName,
      description: obj.description,
      permissions: obj.permissions,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status == "FAILED" ||
        response.data.validationMessages != undefined
      ) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    const { data, errors } = this.state;
    return (
      <React.Fragment>
          <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black">
                  <Card.Title className="card-form-title-dark">
                  {this.state.translations.titleLabel}
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello Permission Group please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <div>
                      {this.renderInput(false, false, "id", "Id", "hidden")}
                      {this.renderInput(
                        true,
                        true,
                        "groupName",
                        this.state.translations.groupNameLabel,
                        this.state.translations.groupNamePlaceholder,
                        "text",
                        (data.id != null),
                        this.state.validationMessage.groupName
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "description",
                        this.state.translations.descriptionLabel,
                        this.state.translations.descriptionPlaceholder,
                        "textarea",
                        false,
                        this.state.validationMessage.description
                      )}
                      {this.renderSelect(
                        true,
                        true,
                        "permissions",
                        this.state.translations.permissionsLabel,
                        this.state.translations.permissionsPlaceholder,
                        thisContext.appPermissionOptions,
                        this.state.validationMessage.permissions
                      )}
                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </div>
                  </Card.Body>
                  <Card.Footer>
                    <div className="form-group row">
                      <div className="text-left col-md-4">
                        {this.renderCancelButton("Cancel", function (e) {
                          e.preventDefault();
                          thisContext.props.history.push(
                            "/admin/permissionGroups"
                          );
                        })}
                      </div>
                      <div className="text-right col-md-4">
                        <span style={{ color: "red" }}>
                          {thisContext.state.commonValidationMessage}
                        </span>
                      </div>
                      <div className="text-right col-md-4">
                        {this.renderButton("Save", this.handleSubmit)}
                      </div>
                    </div>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default PermissionGroupForm;
