import React, { Component } from "react";
import Pagination from "../common/pagination";
import { paginate } from "../common/paginate";
import { get, getAllPaginated, remove, save } from "./userMnoCsraPortalService";
import { checkAuthentication } from "../common/authenticationService";
import { Col, Row, Card } from "react-bootstrap";
// import userMnoCsraPortal from "./userMnoCsraPortal.json";
import UserMnoCsraPortalsTable from "./userMnoCsraPortalTable";
import _ from "lodash";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import UxCrudManager from "../common/UxCrudManager";

class userMnoCsraPortalManager extends UxCrudManager {
  state = {
    isLoading: true,
    requiredPermission: "USER_MNO_CSRA_PORTAL_READ",
    basicUrl: "/admin/userMnoCsraPortals/",
    catagorySelectionLabel: "All UserMnoCsraPortals",
    userPermissions: [],
    categories: [
      "All UserMnoCsraPortals",
      "DRAFT",
      "APPROVED",
      "REJECTED",
      "DELETED",
    ],
    translations: {
      titleLabel: "User Mno Csra Portal",
      homeLabel: "Home",
    },
    currentPage: 0,
    data: [],
    pageSize: 10,
    firstPage: true,
    lastPage: false,
    count: 0,
    currentItem: "All UserMnoCsraPortals",
    sortColumn: { path: "status", order: "asc" },
    totalElements: 0,
  };

  loadData = async () => {
    let thisContext = this;
    thisContext.setState({ isLoading: true });
    await checkAuthentication().then(function (result) {
      if (result) {
        // console.log("current page: " + thisContext.state.currentPage);
        // console.log("page size: " + thisContext.state.pageSize);
        getAllPaginated(
          thisContext.state.currentPage,
          thisContext.state.pageSize
        )
          .then((response) => {
            // console.log("response data: " + JSON.stringify(response.data));
            const { data: responseObj } = response;
            thisContext.setState({
              data: responseObj.data,
              pageSize: responseObj.pageSize,
              currentPage: responseObj.currentPage,
              totalElements: responseObj.totalItems,
              firstPage: true,
              lastPage: true,
              isLoading: false
            });
            thisContext.getPagedData();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        thisContext.props.history.push("/login");
      }
    });
  };

  handleDelete = async (item) => {
    const objData = this.state.data;
    try {
      await this.deleteItem(item.userId);
      const data = this.state.data.filter((m) => m.userId !== item.userId);
      this.setState({ data });
    } catch (error) {
      this.setState({ data: objData });
    }
  };

  saveItem = async (item) => {
    await save(item);
  };

  deleteItem = async (item) => {
    await remove(item);
  };

  handleView = async (item) => {
    // console.log("View...");
    this.props.history.push(this.state.basicUrl + item.userId);
    // console.log("View Loaded..");
  };

  handleEdit = async (item) => {
    // console.log("Edit...");
    this.props.history.push(this.state.basicUrl + item.userId);
    // console.log("Edit Loaded.");
  };

  getPagedData = () => {
    const {
      columns,
      pageSize,
      currentPage,
      currentItem,
      data,
      sortColumn,
      totalElements,
    } = this.state;

    const filtered =
      currentItem && currentItem !== this.state.catagorySelectionLabel
        ? data.filter((dataObj) => dataObj.status === currentItem)
        : data;

    const sortedItems = _.orderBy(
      filtered,
      [sortColumn.path],
      [sortColumn.order]
    );
    const paginatedObjData = paginate(sortedItems, 0, pageSize);
    // console.log("paginatedObjData: "+JSON.stringify(paginatedObjData));

    return { totalCount: totalElements, data: paginatedObjData };
  };

  htmlContent() {
    let thisContext = this;
    const { columns, pageSize, currentPage, sortColumn, firstPage, lastPage } =
      this.state;
    //   const { totalCount, data } = this.getPagedData();
    
    const { totalElements, data } = this.state;
 
    return (
      <React.Fragment>
        {/* <DeleteConfirmModal /> */}
          <Row>
            {/*
             * <Col md="2" xs="2"> <ListGroup
             * currentItem={this.state.currentItem} textProperty="name"
             * valueProperty="_id" items={this.state.categories}
             * onItemSelect={this.handleCategorySelect} /> </Col>
             */}
            <Col md="12" xs="12">
              <Card className="bg-light text-black">
                <Card.Title className="card-form-title-dark">
                  User Mno Csra Portal
                </Card.Title>
                <Card.Header>
                  <Row>
                    <Col style={{ display: "flex", justifyContent: "left" }}>
                      <select
                        value={this.state.currentItem}
                        onChange={function (event) {
                          thisContext.handleCategorySelect(event.target.value);
                        }}
                      >
                        <option>-Select Status-</option>
                        {this.state.categories.map((category) => (
                          <option value={category} key={category}>
                            {category}
                          </option>
                        ))}
                      </select>{" "}
                    </Col>
                    <Col style={{ display: "flex", justifyContent: "right" }}>
                      {this.state.userPermissions.includes(
                        "USER_MNO_CSRA_PORTAL_CREATE"
                      ) && (
                        <button
                          onClick={this.loadCreateForm}
                          className="btn btn-sm btn-success"
                        >
                          <FontAwesomeIcon icon={faPlus} />
                        </button>
                      )}
                    </Col>
                  </Row>
                </Card.Header>
                <Card.Body>
                  {/* <Card.Title>User Request</Card.Title> */}
                  {/* form>(div.form-group>label+input.form-control)*2 */}
                  <UserMnoCsraPortalsTable
                    columns={columns}
                    sortColumn={sortColumn}
                    onSort={this.handleSort}
                    onLikeClick={this.handleChangeChk}
                    data={data}
                    onView={this.handleView}
                    onEdit={this.handleEdit}
                    onApprove={this.handleApprove}
                    onDelete={this.handleDelete}
                    onCreate={this.loadCreateForm}
                  />
                </Card.Body>
                <Card.Footer>
                  <Pagination
                    currentPage={currentPage}
                    itemsCount={totalElements}
                    pageSize={pageSize}
                    firstPage={firstPage}
                    lastPage={lastPage}
                    onPageChange={this.handlePageChange}
                  />
                </Card.Footer>
              </Card>
            </Col>
          </Row>
      </React.Fragment>
    );
  }
}

export default userMnoCsraPortalManager;
