import React, { Component } from "react";
import { createNew } from "../../components/common/publicServices";


import { Container, Row, Form, Breadcrumb, Jumbotron,Col } from "react-bootstrap";
import LoginRegistrationNavbar from "../public/includes/LoginRegistrationNavbar";
import PublicFooter from "../public/includes/PublicFooter";
import properties from '../../common_properties.json';

class Register extends Component {
  state = {
    data: {
      id: "",
      firstName: "",
      lastName: "",
      nid: "",
      dob: "",
      email: "",
      contactNumber: "",
      requestedRole: "",
      districtId: "",
      interestedLocations: "",
      shopName: "",
      shopMainBranchAddress: "",
      password: "",
    },
    welcomeMessage: "",
    warningMessage: "* Please fillup all fields.",
    errors: {},
    locations: []
  };

  districts = [
    { "id": 1, "description": "Dhaka", "districtId": 1 },
    { "id": 2, "description": "Gazipur", "districtId": 2 },
    { "id": 3, "description": "Naogaon", "districtId": 3 },
    { "id": 4, "description": "Rajshahi", "districtId": 4 },
    { "id": 5, "description": "Barishal", "districtId": 5 }
  ];
  locationsAll = [
    { "id": 1, "description": "Location 1", "districtId": 1 },
    { "id": 2, "description": "Location 2", "districtId": 1 },
    { "id": 3, "description": "Location 3", "districtId": 1 },
    { "id": 4, "description": "Location 4", "districtId": 1 },
    { "id": 5, "description": "Location 5", "districtId": 2 },
    { "id": 6, "description": "Location 6", "districtId": 2 },
    { "id": 7, "description": "Location 7", "districtId": 2 },
    { "id": 8, "description": "Location 8", "districtId": 3 },
    { "id": 9, "description": "Location 9", "districtId": 3 },
    { "id": 10, "description": "Location 10", "districtId": 4 },
    { "id": 11, "description": "Location 11", "districtId": 4 },
    { "id": 12, "description": "Location 12", "districtId": 5 },
    { "id": 13, "description": "Location 13", "districtId": 5 },
    { "id": 14, "description": "Location 14", "districtId": 5 },
  ];

  loadLocations(districtId) {
    const result = this.locationsAll.filter(location => location.districtId == districtId);
    this.setState({ locations: result });
  }
  // schema = {
  //   id: Joi.number().allow(null),
  //   firstName: Joi.string().required().label("First Name"),
  //   lastName: Joi.string().required().label("Last Name"),
  //   nid: Joi.string().required().label("nid"),
  //   dob: Joi.string().required().label("dob"),
  //   email: Joi.string().required().label("Email"),
  //   contactNumber: Joi.string().required().label("Contact Number"),
  //   districtId: Joi.string().required().label("District"),
  //   interestedLocations: Joi.string().required().label("Interested Locations"),
  //   shopName: Joi.string().required().label("Shop Name"),
  //   shopMainBranchAddress: Joi.string().required().label("Shop Main Branch Address"),
  //   password: Joi.string().required().label("Password"),
  //   requestedRole: Joi.string().required().label("Requested Role"),
  // };
  async populateData() {
    try {
      this.setState({ data: { id: "9999", requestedRole: "UNKNOWN" } });
    } catch (ex) {
      alert(ex);
    }
  }

  async componentDidMount() {
    await this.populateData();
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      firstName: obj.firstName,
      lastName: obj.lastName,
      nid: obj.nid,
      dob: obj.dob,
      email: obj.email,
      contactNumber: obj.contactNumber,
      districtId: obj.districtId,
      interestedLocations: obj.interestedLocations,
      shopName: obj.shopName,
      shopMainBranchAddress: obj.shopMainBranchAddress,
      password: obj.password,
      requestedRole: obj.requestedRole,
    };
  }
  doSubmit = async (event) => {
    const thisContext = this;
    event.preventDefault();
    try {
      console.log("Validating....:");
      console.log(this.state.data);
      const formData = new FormData(event.target),
        formDataObj = Object.fromEntries(formData.entries());
      // alert(JSON.stringify(formDataObj));
      let finalObj = this.state.data;
      finalObj = formDataObj;
      if (finalObj.firstName == '') {
        thisContext.setState({ warningMessage: '\'First name\' cannot be empty.' });
        return;
      } else if (finalObj.lastName == '') {
        thisContext.setState({ warningMessage: '\'Last name\' cannot be empty.' });
        return;
      } else if (finalObj.nid == '') {
        thisContext.setState({ warningMessage: '\'NID\' cannot be empty.' });
        return;
      } else if (finalObj.dob == '') {
        thisContext.setState({ warningMessage: '\'DOB\' cannot be empty.' });
        return;
      } else if (finalObj.email == '') {
        thisContext.setState({ warningMessage: '\'Email\' cannot be empty.' });
        return;
      } else if (finalObj.contactNumber == '') {
        thisContext.setState({ warningMessage: '\'Contact Number\' cannot be empty.' });
        return;
      } else if (finalObj.districtId == '') {
        thisContext.setState({ warningMessage: '\'District\' cannot be empty.' });
        return;
      } else if (finalObj.interestedLocations == '') {
        thisContext.setState({ warningMessage: '\'Interested Locations\' cannot be empty.' });
        return;
      } else if (finalObj.shopName == '') {
        thisContext.setState({ warningMessage: '\'Shop Name\' cannot be empty.' });
        return;
      } else if (finalObj.shopName == '') {
        thisContext.setState({ warningMessage: '\'Shop Name\' cannot be empty.' });
        return;
      } else if (finalObj.shopMainBranchAddress == '') {
        thisContext.setState({ warningMessage: '\'Shop Main Branch Address\' cannot be empty.' });
        return;
      } else if (finalObj.requestedRole == '') {
        thisContext.setState({ warningMessage: '\'Requested Role\' cannot be empty.' });
        return;
      } else if (finalObj.password == '') {
        thisContext.setState({ warningMessage: '\'Password\' cannot be empty.' });
        return;
      } else if (finalObj.confirmPassword != finalObj.password) {
        thisContext.setState({ warningMessage: 'Password and confirm password not matched.' });
        return;
      } else {
        thisContext.setState({ warningMessage: '' });
      }
      finalObj.groupId = 1;
      delete finalObj.confirmPassword;
      await createNew(finalObj).then(function (response) {
        // alert("Response: " + JSON.stringify(response.data));
        // registration_code
        if (response.data != undefined && response.data.status == "SUCCESS") {
          thisContext.setState({
            warningMessage: 'User request sent, Please Copy Registration Code: ' + response.data.data[0].registrationCode, data: {
              id: "",
              firstName: "",
              lastName: "",
              nid: "",
              dob: "",
              email: "",
              contactNumber: "",
              districtId: "",
              interestedLocations: "",
              shopName: "",
              shopMainBranchAddress: "",
              password: "",
              requestedRole: "",
            }
          });
        } else if (response.data != undefined && response.data.status == "FAILED") {
          thisContext.setState({
            warningMessage: response.data.message
          });
        } else {
          thisContext.setState({
            warningMessage: 'Error Occured ! Please Check Network Connection'
          });
        }
        // thisContext.props.history.push("/registration");

      });
    } catch (error) {
      thisContext.setState({ warningMessage: 'Error occured: ' + error });
    }
  };

  render() {
    const { data, errors } = this.state;
    const thisContext = this;
    return (
      <React.Fragment>
        <Container>
          <LoginRegistrationNavbar
            toggle={this.props.toggle}
            isLoggedIn={this.state.isLoggedIn}
            loginUserEmail={this.state.loginUserEmail}
            loginUserFirstName={this.state.loginUserFirstName}
            loginUserLastName={this.state.loginUserLastName}
            isAdmin={this.state.isAdmin}
            isSupplier={this.state.isSupplier}
            isCustomer={this.state.isCustomer}
            labels={properties.labels.languages.en}
            locations={this.state.locations}
            updateSelectedLocationId={this.updateSelectedLocationId}
            selectedLocationId={this.state.selectedLocationId}
          />
          <Breadcrumb>
            <Breadcrumb.Item>{this.state.welcomeMessage}</Breadcrumb.Item>
          </Breadcrumb>
          <Jumbotron>
          <h1>User / Admin Registration</h1>
            <form onSubmit={this.doSubmit}>
              <Row>
                <div class="col-xs-6 col-sm-6 col-md-6">
                  <Form.Group>
                    <Form.Control type="text" name="firstName" value={this.state.data.firstName} placeholder="First Name" />
                  </Form.Group>
                </div>
                <div class="col-xs-6 col-sm-6 col-md-6">
                  <Form.Group>
                    <Form.Control type="text" name="lastName" value={this.state.data.lastName} placeholder="Last Name" />
                  </Form.Group>
                </div>
</Row>
              <Row>
                <div class="col-xs-6 col-sm-6 col-md-6">
                  <Form.Group>
                    <Form.Control type="email" name="email" value={this.state.data.email} placeholder="Email Address" />
                  </Form.Group>
                </div>
                <div class="col-xs-6 col-sm-6 col-md-6">
                  <Form.Group>
                    <Form.Control type="text" name="contactNumber" value={this.state.data.email} placeholder="Contact Number" />
                  </Form.Group>
                </div>
              </Row>
              <Row>
                <div class="col-xs-6 col-sm-6 col-md-6">

                </div>
                <div class="col-xs-6 col-sm-6 col-md-6">
                  <Form.Group>
                    {/* <Form.Control type="text" name="requestedRole" placeholder="Requested Role" /> */}
                    <select name="requestedRole" value={this.state.data.requestedRole}>
                      <option>-Requested Role-</option>
                      <option value="0XVND_ROLE">User</option>
                      <option value="0XCAD_ROLE">Admin</option>
                      <option value="0XSSAD_ROLE">Super Admin</option>
                    </select>
                  </Form.Group>

                </div>
              </Row>

              <Row>
                <div class="col-xs-6 col-sm-6 col-md-6">
                  <Form.Group>
                    <Form.Control type="password" name="password" placeholder="Password" />
                  </Form.Group>
                </div>
                <div class="col-xs-6 col-sm-6 col-md-6">
                  <Form.Group>
                    <Form.Control type="password" name="confirmPassword" placeholder="Confirm Password" />
                  </Form.Group>
                </div>
              </Row>

              <Row>
                <div style={{ color: "red" }} class="col-xs-6 col-sm-6 col-md-6">
                  {this.state.warningMessage}
                </div>
              </Row>
              <Form.Control type="submit" value="Register" className="btn btn-sm btn-info btn-block" />

            </form>
          </Jumbotron>

          <PublicFooter/>
        </Container>
      </React.Fragment>
    );
  }
}

export default Register;
