import React, { Component } from "react";
import Table from "../common/table";
import { Button, Form, Modal } from "react-bootstrap";
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faPencilAlt, faCheck } from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class UserViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" size="sm" onClick={this.props.onClick}>
          <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Username</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.username}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Password</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.password}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>First Name</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.firstName}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Last Name</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.lastName}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Email</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.email}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Activated</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.activated}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Membership Id</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.membershipId}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Membership Type</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.membershipType}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Last Login Date</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.lastLoginDate}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Company Id</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.companyId}</div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <b>
                    <label>Active Language</label>
                  </b>
                </div>
                <div className="col-md-6">{this.props.data.activeLanguage}</div>
              </div>

              {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
            </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class UserTable extends UxCrudTable {
  state = {
    userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "Users",
    body: "",
    data: [],
    title: "User Details",
    translations: {
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      titleLabel: "User",
      nameLabel: "Name",
      usernameLabel: "Username",
      passwordLabel: "Password",
      firstNameLabel: "First Name",
      lastNameLabel: "Last Name",
      designationLabel: "Designation",
      emailLabel: "Email",
      mobileNoLabel: "Mobile No",
      activatedLabel: "Activated ?",
      authoritiesLabel: "Authorities",
      membershipIdLabel: "Membership Id",
      membershipTypeLabel: "Membership Type",
      lastLoginDateLabel: "Last Login Date",
      companyIdLabel: "Company",
      activeLanguageLabel: "Active Language",
    },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,

          usernameLabel: translationsObj["i18n.user.username.label"]
            ? translationsObj["i18n.user.username.label"]
            : thisContext.state.translations.usernameLabel,

            nameLabel: translationsObj["i18n.user.name.label"]
            ? translationsObj["i18n.user.name.label"]
            : thisContext.state.translations.nameLabel,

            emailLabel: translationsObj["i18n.user.email.label"]
              ? translationsObj["i18n.user.email.label"]
              : thisContext.state.translations.emailLabel,

          mobileNoLabel: translationsObj["i18n.user.mobileNo.label"]
            ? translationsObj["i18n.user.mobileNo.label"]
            : thisContext.state.translations.mobileNoLabel,
            
          authoritiesLabel: translationsObj["i18n.user.authorities.label"]
          ? translationsObj["i18n.user.authorities.label"]
          : thisContext.state.translations.authoritiesLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  async loadColumns() {
    const thisContext = this;

    let columns = [
      {
        path: "username",
        label: this.state.translations.usernameLabel,
      },
      {
        path: "name",
        label: this.state.translations.nameLabel,
        content: (item) => (
          <>
            {item.firstName} {item.lastName}
          </>
        ),
      },
      {
        path: "mobileNo",
        label: this.state.translations.mobileNoLabel,
      },
      {
        path: "email",
        label: this.state.translations.emailLabel,
      },
      {
        path: "authorities",
        label: this.state.translations.authoritiesLabel,
        content: (item) => (
          <>
            {item.authorities.map((authority) => (
              <>
                <Button variant="warning" size="sm">
                  {authority.name}
                </Button>
                &nbsp;
              </>
            ))}
          </>
        ),
      },
      {
        key: "view",
        label: this.state.translations.viewLabel,
        content: (item) => (
          <UserViewModal
            itemId={this.state.itemId}
            show={this.state.showView}
            title={this.state.title}
            body={this.state.body}
            data={this.state.data}
            onClick={() => this.handleViewShow(item)}
            onHide={this.handleViewClose}
          />
        ),
      },
    ];

    if (thisContext.state.userPermissions.includes("USER_UPDATE")) {
      columns.push({
        key: "edit",
        label: this.state.translations.editLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onEdit(item)}
            className="btn btn-sm btn-info"
          >
            <FontAwesomeIcon icon={faPencilAlt} />
          </button>
        ),
      });
    }

    // if (thisContext.state.userPermissions.includes("USER_APPROVE")) {
    //   columns.push({
    //     key: "approve",
    //     label: this.state.translations.approveLabel,
    //     content: (item) => (
    //       <button
    //         onClick={() => this.props.onApprove(item)}
    //         className="btn btn-sm btn-success"
    //         disabled={item.status == "APPROVED"}
    //       >
    //         <FontAwesomeIcon icon={faCheck} />
    //       </button>
    //     ),
    //   });
    // }

    if (thisContext.state.userPermissions.includes("USER_DELETE")) {
      columns.push({
        key: "delete",
        label: this.state.translations.deleteLabel,
        content: (item) => (
          <DeleteConfirmModal
            disabled={item.status == "APPROVED"}
            show={this.state.showDeleteConfirm}
            item={this.state.item}
            onClick={() => this.handleDeleteModalShow(item)}
            onHide={this.handleDeleteConfirmClose}
            onConfirm={(item) => this.handleDeleteFromModal(item)}
          />
        ),
      });
    }

    this.setState({ columns: columns });
  }

}

export default UserTable;
