import React, { Component } from "react";
import Table from "../common/table";
import { Button, Form, Modal } from 'react-bootstrap';
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faPencilAlt,
  faCheck
} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class MnoCsraAuthorizationKeyViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" size="sm" onClick={this.props.onClick}>
          <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form>
              <div className="form-group row">
                <div className="col-md-6">
                  <b><label>Key Collection Date</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.keyCollectionDate}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Authorization Key</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.authorizationKey}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Expire Date</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.expireDate}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Remarks</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.remarks}
                </div>
              </div>

              {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
            </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class MnoCsraAuthorizationKeyTable extends UxCrudTable {
  state = {
    userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    title: "Mno Csra Authorization Key Details",
    translations: {
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      titleLabel: "Mno Csra Authorization Key",
      mnoIdLabel: "MNO",
      keyCollectionDateLabel: "Key Collection Date",
      authorizationKeyLabel: "Authorization Key",
      expireDateLabel: "Expire Date",
      remarksLabel: "Remarks",
    },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
          ? translationsObj["i18n.common.viewLabel"]
          : thisContext.state.translations.viewLabel,

        editLabel: translationsObj["i18n.common.editLabel"]
          ? translationsObj["i18n.common.editLabel"]
          : thisContext.state.translations.editLabel,

        approveLabel: translationsObj["i18n.common.approveLabel"]
          ? translationsObj["i18n.common.approveLabel"]
          : thisContext.state.translations.approveLabel,

        deleteLabel: translationsObj["i18n.common.deleteLabel"]
          ? translationsObj["i18n.common.deleteLabel"]
          : thisContext.state.translations.deleteLabel,

          titleLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.titleLabel"
          ]
            ? translationsObj["i18n.mnoCsraAuthorizationKey.titleLabel"]
            : thisContext.state.translations.titleLabel,

            mnoIdLabel: translationsObj[
              "i18n.mnoCsraAuthorizationKey.mnoId.label"
            ]
              ? translationsObj["i18n.mnoCsraAuthorizationKey.mnoId.label"]
              : thisContext.state.translations.mnoIdLabel,
          keyCollectionDateLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.keyCollectionDate.label"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.keyCollectionDate.label"
              ]
            : thisContext.state.translations.keyCollectionDateLabel,
          authorizationKeyLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.authorizationKey.label"
          ]
            ? translationsObj[
                "i18n.mnoCsraAuthorizationKey.authorizationKey.label"
              ]
            : thisContext.state.translations.authorizationKeyLabel,

          expireDateLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.expireDate.label"
          ]
            ? translationsObj["i18n.mnoCsraAuthorizationKey.expireDate.label"]
            : thisContext.state.translations.expireDateLabel,
          remarksLabel: translationsObj[
            "i18n.mnoCsraAuthorizationKey.remarks.label"
          ]
            ? translationsObj["i18n.mnoCsraAuthorizationKey.remarks.label"]
            : thisContext.state.translations.remarksLabel,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  async loadColumns() {
    const thisContext = this;
    let columns = [
      { path: "id", label: "Id" },
      {
        path: "keyCollectionDate",
        label: this.state.translations.keyCollectionDateLabel,
      }, {
        path: "authorizationKey",
        label: this.state.translations.authorizationKeyLabel,
      }, {
        path: "expireDate",
        label: this.state.translations.expireDateLabel,
      }, {
        path: "remarks",
        label: this.state.translations.remarksLabel,
      },
      {
        key: "view",
        label: this.state.translations.viewLabel,
        content: (item) => (
          <MnoCsraAuthorizationKeyViewModal
            itemId={this.state.itemId}
            show={this.state.showView}
            title={this.state.title}
            body={this.state.body}
            data={this.state.data}
            onClick={() => this.handleViewShow(item)}
            onHide={this.handleViewClose}
          />
        ),
      },
    ];

    if (thisContext.state.userPermissions.includes("MNO_CSRA_AUTHORIZATION_KEY_UPDATE")) {
      columns.push({
        key: "edit",
        label: this.state.translations.editLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onEdit(item)}
            className="btn btn-sm btn-info"
          >
            <FontAwesomeIcon icon={faPencilAlt} />
          </button>
        ),
      });
    }

    if (thisContext.state.userPermissions.includes("MNO_CSRA_AUTHORIZATION_KEY_APPROVE")) {
      columns.push({
        key: "approve",
        label: this.state.translations.approveLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onApprove(item)}
            className="btn btn-sm btn-success"
            disabled={item.status == "APPROVED"}
          >
            <FontAwesomeIcon icon={faCheck} />
          </button>
        ),
      });
    }

    if (thisContext.state.userPermissions.includes("MNO_CSRA_AUTHORIZATION_KEY_DELETE")) {
      columns.push({
        key: "delete",
        label: this.state.translations.deleteLabel,
        content: (item) => (
          <DeleteConfirmModal
            disabled={item.status == "APPROVED"}
            show={this.state.showDeleteConfirm}
            item={this.state.item}
            onClick={() => this.handleDeleteModalShow(item)}
            onHide={this.handleDeleteConfirmClose}
            onConfirm={(item) => this.handleDeleteFromModal(item)}
          />
        ),
      });
    }

    this.setState({ columns: columns });
  }

}

export default MnoCsraAuthorizationKeyTable;
