import React, { useState } from 'react';

const DraggableObject = ({ title, description }) => {
  const [isDragging, setIsDragging] = useState(false);
  const [dragStartX, setDragStartX] = useState(0);
  const [dragStartY, setDragStartY] = useState(0);
  const [offsetX, setOffsetX] = useState(60);
  const [offsetY, setOffsetY] = useState(35);

  const handleDragStart = (e) => {
    setIsDragging(true);
    setDragStartX(e.clientX - offsetX);
    setDragStartY(e.clientY - offsetY);
  };

  const handleDragEnd = () => {
    setIsDragging(false);
  };

  const handleDrag = (e, title) => {
    if (isDragging) {
      console.log("employee: " + title);
      e.dataTransfer.setData("employeeName", title);

      const newOffsetX = e.clientX - dragStartX;
      const newOffsetY = e.clientY - dragStartY;
      setOffsetX(newOffsetX);
      setOffsetY(newOffsetY);
    }
  };

  const handleDragOver = (e) => {
    e.preventDefault();
  };

  const handleDrop = (e) => {
    let employee = e.dataTransfer.getData("employeeName");
    console.log(JSON.stringify(employee));
    console.log("Target Employee: " + employee);
    console.log("Target Department: N/A");

    e.preventDefault();
    setIsDragging(false);
  };

  return (
    <div
      style={{
        position: 'relative',
        top: offsetY,
        left: offsetX,
        border: '1px black solid',
        float: '2px',
        transform: `translate(-50%, -50%)`,
        width: '150px',
        height: '100px',
        backgroundColor: 'lightgray',
      }}
      onDragOver={handleDragOver}
      onDrop={handleDrop}
    >
      <div
        style={{
          position: 'relative',
          width: '100%',
          height: '100%',
          cursor: 'grab',
        }}
        draggable="true"
        onDragStart={handleDragStart}
        onDrag={(e) => handleDrag(e, title)}
        onDragEnd={handleDragEnd}
      >
        <h4 style={{ textAlign: 'center', marginTop: '10px' }}>{title}</h4>
        <p style={{ textAlign: 'center' }}>{description}</p>
      </div>
    </div>
  );
};

export default DraggableObject;
