import React, { Component } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import { verifyRegistration } from "../common/publicServices";


class RegistrationVerify extends Component {
    state = {
        registrationVerifyMessage: "",
        requestedEmail: "",
        registrationCode: ""
    };

    // schema = {
    //     id: Joi.number().allow(null),
    //     requestedEmail: Joi.string().required().label("Requested Email"),
    //     registrationCode: Joi.string().required().label("Registration Code")
    // };
    doSubmit = async (event) => {
        event.preventDefault();
        const formData = new FormData(event.target),
            formDataObj = Object.fromEntries(formData.entries());
        // let finalObj = this.state.data;
        const thisContext = this;
        let finalObj = formDataObj;
        let email = finalObj.requestedEmail;
        let registrationCode = finalObj.registrationCode;
        if(email == null || email == ''){
            thisContext.setState({ registrationVerifyMessage: 'Email Cannot be Empty' });
            return;
        } else if(registrationCode == null || registrationCode == '') {
            thisContext.setState({ registrationVerifyMessage: 'Registration Code Cannot be Empty' });
            return;
        }
        verifyRegistration(email, registrationCode).then(function (response) {
            thisContext.setState({ registrationVerifyMessage: response.data.message });
        });
    }
    render() {
        return (
            <React.Fragment>
                <Container id="registrationVerify" classname="container-fluid bg-grey">
                    <h2 classname="text-center">Verify Registration Info</h2>
                    <Row classname="row">
                        <Col classname="col-sm-5">
                            <p>
                                <span classname="glyphicon glyphicon-envelope"></span>{" "}
                                For Special Contact: mail@ecommerce.com
                            </p>
                        </Col>
                        <Col classname="col-sm-7 slideanim">
                            <form onSubmit={this.doSubmit}>
                                <Row classname="row">
                                    <Col classname="col-sm-12 form-group">
                                        <Form.Group controlId="registrationCode">
                                            <Form.Label>Registration Code</Form.Label>
                                            <Form.Control name="registrationCode" type="text" placeholder="Enter Registration Code" />
                                            <Form.Text className="text-muted">
                                                Please Enter your registration code.
                                            </Form.Text>
                                        </Form.Group>
                                    </Col>
                                </Row>
                                <Row classname="row">
                                    <Col classname="col-sm-12 form-group">
                                        <Form.Group controlId="requestedEmail">
                                            <Form.Label>Email</Form.Label>
                                            <Form.Control name="requestedEmail" type="email" placeholder="Enter Email" />
                                            <Form.Text className="text-muted">
                                                Please Enter your email which you requested for user.
                                            </Form.Text>
                                            <Form.Text className="text-muted">
                                                {this.state.registrationVerifyMessage}
                                            </Form.Text>
                                        </Form.Group>
                                    </Col>
                                </Row>

                                <Row classname="row">
                                    <Col classname="col-sm-12 form-group">
                                        {/* <button onClick={this.verifyRegistration()} classname="btn btn-default pull-right" type="submit">
                                            Verify
                                        </button> */}
                                        <Form.Control type="submit" value="Verify" className="btn btn-sm btn-info btn-block" />
                                    </Col>
                                </Row>
                            </form>
                        </Col>
                    </Row>
                </Container>
            </React.Fragment>
        );
    }
}

export default RegistrationVerify;
