import React, { Component } from "react";
import Pagination from "../common/pagination";
// import { paginate } from "../common/paginate";
import { get, getAllPaginated, remove, save } from "./variantValuesService";
import { checkAuthentication } from "../common/authenticationService";
// import ListGroup from "../common/listGroup";
import Container from "react-bootstrap/Container";
import { Col, Row, Card } from "react-bootstrap";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import { NavLink } from "react-router-dom";
// import variantValues from "./variantValues.json";
import VariantValuessTable from "./variantValuesTable";
import _ from "lodash";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import UxCrudManager from "../common/UxCrudManager";

class VariantValuesManager extends UxCrudManager {
	state = {
		isLoading : true,
    	requiredPermission: "VARIANT_VALUES_READ",
		moduleUrl: "/admin",
		basicUrl: "/admin/variantValuess/",
    	catagorySelectionLabel: "All VariantValuess",
		userPermissions: [],
		data: [],
		categories: [
			"All VariantValuess",
			"DRAFT",
			"APPROVED",
			"REJECTED",
			"DELETED"
		],
		currentPage: 0,
		pageSize: 5,
		firstPage: true,
		lastPage: false,
		count: 0,
		currentItem: "All VariantValuess",
		sortColumn: { path: "status", order: "asc" },
		translations: {
		  "homeLabel":"Home",
	      "titleLabel":"Variant Values"
	    },
		totalElements: 0,
	};


	loadData = async () => {
		let thisContext = this;
		thisContext.setState({ isLoading: true });
		await checkAuthentication().then(function (result) {
			if (result) {
				// console.log("current page: " + thisContext.state.currentPage);
				// console.log("page size: " + thisContext.state.pageSize);
				getAllPaginated(thisContext.state.currentPage, thisContext.state.pageSize)
					.then((response) => {
						const { data: responseObj } = response;
						thisContext.setState({
							data: responseObj.content,
							pageSize: responseObj.pageable.pageSize,
							currentPage: responseObj.pageable.pageNumber,
							totalElements: responseObj.totalElements,
							firstPage: responseObj.first,
							lastPage: responseObj.last,
							isLoading: false
						});
						thisContext.getPagedData();
					})
					.catch((e) => {
						console.log(e);
					});
			} else {
				thisContext.props.history.push("/login");
			}
		});
	}
	
	async loadTranslations() {
	    const thisContext = this;
	    let translations = localStorage.getItem("translations");
	    if (translations) {
	      let translationsObj = JSON.parse(translations);
	      thisContext.setState({
	        translations: {
		      "homeLabel": translationsObj["i18n.home.label"] ? translationsObj["i18n.home.label"] : thisContext.state.translations.homeLabel,
	          "titleLabel": translationsObj["i18n.variantValues.titleLabel"]
	            ? translationsObj["i18n.variantValues.titleLabel"]
	            : thisContext.state.translations.titleLabel,
	        },
	      });
	    } else {
			console.log("Alert!!!, Translations not found!");
	    }
	  }

	 saveItem = async (item) => {
	    await save(item);
	 }
	
	 deleteItem = async (item) => {
	    await remove(item);
	 }

	htmlContent() {
		let thisContext = this;
		const { columns, pageSize, currentPage, sortColumn, firstPage, lastPage } = this.state;
		// const { totalCount, data } = this.getPagedData();
		const { totalElements, data } = this.state;


		return (
			<React.Fragment>
				{/* <DeleteConfirmModal /> */}
				<Row>
					{/*
			 * <Col md="2" xs="2"> <ListGroup
			 * currentItem={this.state.currentItem} textProperty="name"
			 * valueProperty="_id" items={this.state.categories}
			 * onItemSelect={this.handleCategorySelect} /> </Col>
			 */}
					<Col md="12" xs="12">
						<Card className="bg-light text-black">
						<Card.Title className="card-form-title-dark">{this.state.translations.titleLabel}</Card.Title>
							<Card.Header>
								<Row>
			                    <Col  style={{display:'flex', justifyContent:'left'}}>
			                      <select
			                        value={this.state.currentItem}
			                        onChange={function (event) {
			                          thisContext.handleCategorySelect(event.target.value);
			                        }}
			                      >
			                        <option>-Select Status-</option>
			                        {this.state.categories.map((category) => (
			                          <option value={category} key={category}>
			                            {category}
			                          </option>
			                        ))}
			                      </select>{" "}
			                      </Col>
								  <Col  style={{display:'flex', justifyContent:'right'}}>
			                      {this.state.userPermissions.includes("VARIANT_VALUES_CREATE") && (
			                        <button
			                          onClick={this.loadCreateForm}
			                          className="btn btn-success"
			                        >
			                          <FontAwesomeIcon icon={faPlus} />
			                        </button>
			                      )}
			                    </Col>
			                  </Row>
							</Card.Header>
							<Card.Body>
								{/* <Card.Title>User Request</Card.Title> */}
								{/* form>(div.form-group>label+input.form-control)*2 */}
								<VariantValuessTable
									columns={columns}
									sortColumn={sortColumn}
									onSort={this.handleSort}
									onLikeClick={this.handleChangeChk}
									data={data}
									onView={this.handleView}
									onEdit={this.handleEdit}
									onApprove={this.handleApprove}
									onDelete={this.handleDelete}
									onCreate={this.loadCreateForm}
								/>
							</Card.Body>
							<Card.Footer>
								<Pagination
									currentPage={currentPage}
									itemsCount={totalElements}
									pageSize={pageSize}
									firstPage={firstPage}
									lastPage={lastPage}
									onPageChange={this.handlePageChange}
								/>
							</Card.Footer>
						</Card>
					</Col>
				</Row>
			</React.Fragment>
		);
	}
}

export default VariantValuesManager;
