import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./mnoCsraPortalService";
// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import { NavLink } from "react-router-dom";
import FileBase64 from "react-file-base64";
import { getAll as getAllMno } from "../mno/mnoService";

class MnoCsraPortalForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["MNO_CSRA_PORTAL_CREATE","MNO_CSRA_PORTAL_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/mnoCsraPortals/",
    userPermissions: [],
    data: {
      id: null,
      mnoId: "",
      mnoPortalTitle: "",
      csraPortalDefaultUrl: "",
      mnoPortalLogoBase64: "",
      mnoPortalRequestingUrl: "",
    },
    errors: {},
    validationMessage: {
      mno: "",
      mnoPortalTitle: "",
      csraPortalDefaultUrl: "",
      mnoPortalLogoBase64: "",
    },
    commonValidationMessage: "",
    translations: {
      // orgNameLabel: "Organization Name",
      titleLabel: "Mno Csra Portal",
      mnoIdLabel: "MNO",
      mnoIdPlaceholder: "Select MNO",
      mnoPortalTitleLabel: "MNO Portal Title",
      mnoPortalTitlePlaceholder: "Enter MNO Portal Title",
      csraPortalDefaultUrlLabel: "CSRA Portal Default URL",
      csraPortalDefaultUrlPlaceholder: "Enter CSRA Portal Default URL",
      mnoPortalLogoBase64Label: "MNO Portal Logo",
      mnoPortalLogoBase64Placeholder: "Enter MNO Portal Logo",
      mnoPortalRequestingUrlLabel: "Portal Integration URL",
      mnoPortalRequestingUrlPlaceholder: "Enter Portal Integration URL",
    },
  };
  mnoOptions = [];

  /* schema = {
    id: Joi.number().allow(null),
    mno: Joi.number().label("Mno"),mnoPortalTitle: Joi.string().required().label({this.state.translations.mnoPortalTitleLabel}),csraPortalDefaultUrl: Joi.string().required().label({this.state.translations.csraPortalDefaultUrlLabel}),mnoPortalLogoBase64: Joi.string().required().label({this.state.translations.mnoPortalLogoBase64Label}),mnoPortalRequestingUrl: Joi.string().label({this.state.translations.mnoPortalRequestingUrlLabel}),
  };
*/
  async populateData() {
    try {
      const thisContext = this;
      await getAllMno().then(function (result) {
        for (var i = 0; i < result.data.length; i++) {
          thisContext.mnoOptions.push({
            value: result.data[i].id,
            label: result.data[i].description,
          });
        }
      });
      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
          this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          // orgNameLabel: translationsObj["i18n.organization.orgName.label"] ? translationsObj["i18n.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          titleLabel: translationsObj["i18n.mnoCsraPortal.titleLabel"]
          ? translationsObj["i18n.mnoCsraPortal.titleLabel"]
          : thisContext.state.translations.titleLabel,

          mnoIdLabel: translationsObj["i18n.mnoCsraPortal.mnoId.label"]
            ? translationsObj["i18n.mnoCsraPortal.mnoId.label"]
            : thisContext.state.translations.mnoIdLabel,
          mnoIdPlaceholder: translationsObj[
            "i18n.mnoCsraPortal.mnoId.placeholder"
          ]
            ? translationsObj["i18n.mnoCsraPortal.mnoId.placeholder"]
            : thisContext.state.translations.mnoIdPlaceholder,
          mnoPortalTitleLabel: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalTitle.label"
          ]
            ? translationsObj["i18n.mnoCsraPortal.mnoPortalTitle.label"]
            : thisContext.state.translations.mnoPortalTitleLabel,
          mnoPortalTitlePlaceholder: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalTitle.placeholder"
          ]
            ? translationsObj["i18n.mnoCsraPortal.mnoPortalTitle.placeholder"]
            : thisContext.state.translations.mnoPortalTitlePlaceholder,
          csraPortalDefaultUrlLabel: translationsObj[
            "i18n.mnoCsraPortal.csraPortalDefaultUrl.label"
          ]
            ? translationsObj["i18n.mnoCsraPortal.csraPortalDefaultUrl.label"]
            : thisContext.state.translations.csraPortalDefaultUrlLabel,
          csraPortalDefaultUrlPlaceholder: translationsObj[
            "i18n.mnoCsraPortal.csraPortalDefaultUrl.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraPortal.csraPortalDefaultUrl.placeholder"
              ]
            : thisContext.state.translations.csraPortalDefaultUrlPlaceholder,
          mnoPortalLogoBase64Label: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalLogoBase64.label"
          ]
            ? translationsObj["i18n.mnoCsraPortal.mnoPortalLogoBase64.label"]
            : thisContext.state.translations.mnoPortalLogoBase64Label,
          mnoPortalLogoBase64Placeholder: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalLogoBase64.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraPortal.mnoPortalLogoBase64.placeholder"
              ]
            : thisContext.state.translations.mnoPortalLogoBase64Placeholder,
          mnoPortalRequestingUrlLabel: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalRequestingUrl.label"
          ]
            ? translationsObj["i18n.mnoCsraPortal.mnoPortalRequestingUrl.label"]
            : thisContext.state.translations.mnoPortalRequestingUrlLabel,
          mnoPortalRequestingUrlPlaceholder: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalRequestingUrl.placeholder"
          ]
            ? translationsObj[
                "i18n.mnoCsraPortal.mnoPortalRequestingUrl.placeholder"
              ]
            : thisContext.state.translations.mnoPortalRequestingUrlPlaceholder,
        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(
        thisContext.state.userPermissions.includes("MNO_CSRA_PORTAL_CREATE") ||
        thisContext.state.userPermissions.includes("MNO_CSRA_PORTAL_UPDATE")
      )
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }

    try {
      await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      mnoId: obj.mnoId,
      mnoPortalTitle: obj.mnoPortalTitle,
      csraPortalDefaultUrl: obj.csraPortalDefaultUrl,
      mnoPortalLogoBase64: obj.mnoPortalLogoBase64,
      mnoPortalRequestingUrl: obj.mnoPortalRequestingUrl,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status == "FAILED" ||
        response.data.validationMessages != undefined
      ) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    const { data, errors } = this.state;
    return (
      <React.Fragment>
          <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black">
                  <Card.Title className="card-form-title-dark">
                  {this.state.translations.titleLabel}
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello Mno Csra Portal please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <div>
                      {this.renderInput(false, false, "id", "Id", "hidden")}
                      {this.renderSelect(
                        true,
                        false,
                        "mnoId",
                        this.state.translations.mnoIdLabel,
                        this.state.translations.mnoIdPlaceholder,
                        thisContext.mnoOptions,
                        this.state.validationMessage.mnoId
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "mnoPortalTitle",
                        this.state.translations.mnoPortalTitleLabel,
                        this.state.translations.mnoPortalTitlePlaceholder,
                        "text",
                        false,
                        this.state.validationMessage.mnoPortalTitle
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "csraPortalDefaultUrl",
                        this.state.translations.csraPortalDefaultUrlLabel,
                        this.state.translations.csraPortalDefaultUrlPlaceholder,
                        "text",
                        false,
                        this.state.validationMessage.csraPortalDefaultUrl
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "mnoPortalLogoBase64",
                        this.state.translations.mnoPortalLogoBase64Label,
                        this.state.translations.mnoPortalLogoBase64Placeholder,
                        "textarea",
                        false,
                        this.state.validationMessage.mnoPortalLogoBase64
                      )}
                      {this.renderInput(
                        false,
                        true,
                        "mnoPortalRequestingUrl",
                        this.state.translations.mnoPortalRequestingUrlLabel,
                        this.state.translations
                          .mnoPortalRequestingUrlPlaceholder,
                        "text",
                        false,
                        this.state.validationMessage.mnoPortalRequestingUrl
                      )}

                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </div>
                  </Card.Body>
                  <Card.Footer>
                    <div className="form-group row">
                      <div className="text-left col-md-4">
                        {this.renderCancelButton("Cancel", function (e) {
                          e.preventDefault();
                          thisContext.props.history.push(
                            "/admin/mnoCsraPortals"
                          );
                        })}
                      </div>
                      <div className="text-right col-md-4">
                        <span style={{ color: "red" }}>
                          {thisContext.state.commonValidationMessage}
                        </span>
                      </div>
                      <div className="text-right col-md-4">
                        {this.renderButton("Save", this.handleSubmit)}
                      </div>
                    </div>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default MnoCsraPortalForm;
