import React, { useEffect, useState } from "react";
// import { Table } from "react-bootstrap";
import axios from 'axios';
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';
import { getConfiguredCsraPortals, getMNOCsraIntegrationUrl } from "../common/dashboardService";

const DashboardData = ({ userPermissions, integrationUsingWebserver }) => {
  // State variables to store inventory data
  const [configuredCsraPortals, setConfiguredCsraPortals] = useState([]);
  const [isLoading, setIsLoading] = useState([]);
  const [isIntegrationSuccess, setIsIntegrationSuccess] = useState(true);
  const [integrationFailedMessage, setIntegrationFailedMessage] = useState('');


  // Function to fetch dashboard data from the server
  const fetchConfiguredCsraPortals = async () => {
    try {
      setIsLoading(true);
      await getConfiguredCsraPortals().then(function (response) {
        const fetchObject = response.data;
        setConfiguredCsraPortals(fetchObject);
        setIsLoading(false);
      });
    } catch (error) {
      console.error("Error fetching dashboard data:", error);
    }
  };

  const getCsraUrlFromWebclient = (configuredCsraPortal) => {
    setIsIntegrationSuccess(true);
    const userStr = localStorage.getItem('user');
    let user = JSON.parse(userStr);

    const requestBody = {
      "email": user.email,
      "mobile_number": user.mobileNo
    };

    let keyName = configuredCsraPortal.authorizationKey;
    let valueText = configuredCsraPortal.authorizationValue;

    var headers = {
      "Content-Type": "application/json",
    };

    headers[keyName] = valueText;

    const tempAnchor = document.createElement('a');
    return new Promise((resolve, reject) => {
      axios
        .post(configuredCsraPortal.mnoPortalRequestingUrl, JSON.stringify(requestBody), {
          headers: headers,
          redirect: "follow"
        })
        .then(response => {
          tempAnchor.href = response.data.redirection_url;
          tempAnchor.target = '_blank';
          tempAnchor.click();
          resolve(true);
        })
        .then(data => {
          console.log(data);
          resolve(true);
        })
        .catch(error => {
          // window.location.href = configuredCsraPortal.mnoPortalUrl;
          tempAnchor.href = configuredCsraPortal.mnoPortalUrl;
          tempAnchor.target = '_blank';
          tempAnchor.click();
          console.error(error);
          resolve(false);
        });

    });
  };

  const getCsraUrlFromWebserver = async (configuredCsraPortal) => {
    setIsIntegrationSuccess(true);
    const mnoId = configuredCsraPortal.mnoId;
    const tempAnchor = document.createElement('a');
    try {
      setIsLoading(true);
      await getMNOCsraIntegrationUrl(mnoId).then(function (response) {
        const fetchObject = response.data;
        if (fetchObject.is_error == 0) {
          // window.location.href = fetchObject.redirectUrl;
          setIsLoading(false);
          tempAnchor.href = fetchObject.redirectUrl;
          tempAnchor.target = '_blank';
          tempAnchor.click();
        } else {
          console.log("No integration url found!");
          setIntegrationFailedMessage(fetchObject.message);
          setIsIntegrationSuccess(false);
          setIsLoading(false);
        }

      });
    } catch (error) {
      console.error("Error fetching dashboard data:", error);
      setIsIntegrationSuccess(false);
      setIntegrationFailedMessage('Integration not done yet.');
      setIsLoading(false);
    }
  };

  // Fetch dashboard data when the component mounts
  useEffect(() => {
    fetchConfiguredCsraPortals();
    setIsIntegrationSuccess(true);
  }, []);

  const createKey = (item, column) => {
    return item.id + (column.path || column.key);
  };

  if (isLoading) {
    return <Spinner animation="border" role="status"><span className="visually-hidden"></span></Spinner>;
  }

  return (
    <>
      {userPermissions.includes("APP_DASHBOARD_READ") && (
        <div>
          <table align="center" border="0" cellSpacing={"30"} cellPadding={"30"}>
            <tbody>
              <tr>
                {configuredCsraPortals.map((configuredCsraPortal) => (
                  <td key={configuredCsraPortal.mnoId}>
                    <p>
                      {integrationUsingWebserver && (
                        <a style={{  cursor: 'pointer' }} 
                          target="_BLANK"
                          title=""
                          onClick={() => getCsraUrlFromWebserver(configuredCsraPortal)}
                        >
                          <img
                            width={"170px"}
                            alt="2024-02-08-03-44-68ceff24442538e68a23b0b9322693c0"
                            src={configuredCsraPortal.mnoPortalLogoUrl}
                          />
                        </a>
                      )}

                      {!integrationUsingWebserver && (
                        <a style={{  cursor: 'pointer' }} 
                          target="_BLANK"
                          title=""
                          onClick={() => getCsraUrlFromWebclient(configuredCsraPortal)}
                        >
                          <img
                            width={"170px"}
                            alt="2024-02-08-03-44-68ceff24442538e68a23b0b9322693c0"
                            src={configuredCsraPortal.mnoPortalLogoUrl}
                          />
                        </a>
                      )}

                    </p>
                  </td>
                ))}
              </tr>
              <tr>
                {configuredCsraPortals.map((configuredCsraPortal) => (
                  <td key={configuredCsraPortal.mnoName}>
                    {integrationUsingWebserver && (
                      <a style={{  cursor: 'pointer' }} target="_blank" rel="noopener noreferrer"
                        onClick={() => getCsraUrlFromWebserver(configuredCsraPortal)}>
                        {configuredCsraPortal.mnoCsraTitle}
                      </a>
                    )}

                    {!integrationUsingWebserver && (
                      <a style={{  cursor: 'pointer' }} target="_blank" rel="noopener noreferrer"
                        onClick={() => getCsraUrlFromWebclient(configuredCsraPortal)}>
                        {configuredCsraPortal.mnoCsraTitle}
                      </a>
                    )}
                  </td>
                ))}
              </tr>
            </tbody>
          </table>
        </div>
      )}
      {!isIntegrationSuccess && (
        <Alert variant="danger" onClose={() => setIsIntegrationSuccess(true)} dismissible>
          <Alert.Heading>Oh snap! You got an error!</Alert.Heading>
          <p>
            {integrationFailedMessage}
          </p>
        </Alert>
      )}
    </>
  );
};

export default DashboardData;
