import http from "../common/httpService";
import config from "../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/mnoCsraAuthorizationKeys";

function mnoCsraAuthorizationKeyUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}
export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(mnoCsraAuthorizationKeyId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(mnoCsraAuthorizationKeyUrl(mnoCsraAuthorizationKeyId), options);
}

export function save(datObj) {
	return datObj.name != null ? update(datObj) : create(datObj);
}

export function create(mnoCsraAuthorizationKey) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, mnoCsraAuthorizationKey, options);
}

export function update(mnoCsraAuthorizationKey) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, mnoCsraAuthorizationKey, options);
}

export function remove(mnoCsraAuthorizationKeyId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(mnoCsraAuthorizationKeyUrl(mnoCsraAuthorizationKeyId), options);
}

