import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./translationsService";
// import Joi from "joi-browser";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
// import FileBase64 from "react-file-base64";

class TranslationsForm extends UxCrudForm {
  state = {
    isLoading: true,
    requiredPermissions: ["TRANSLATIONS_CREATE","TRANSLATIONS_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/translationss/",
    userPermissions: [],
    data: {
      id: null,
      languageCode: "",
      groupName: "",
      keyName: "",
      valueText: "",
      description: "",
    },
    errors: {},
    translations: {
      "titleLabel": "Translations",
      "languageCodeLabel": "Language Code",
      "languageCodePlaceholder": "Enter Language Code",
      "groupNameLabel": "Group Name",
      "groupNamePlaceholder": "Enter Group Name",
      "keyNameLabel": "Key Name",
      "keyNamePlaceholder": "Enter Key Name",
      "valueTextLabel": "Value Text",
      "valueTextPlaceholder": "Enter Value Text",
      "descriptionLabel": "Description",
      "descriptionPlaceholder": "Enter Description",
    },
    validationMessage: {
      languageCode: "",
      groupName: "",
      keyName: "",
    },
    commonValidationMessage: "",
  };

  /* schema = {
    id: Joi.number().allow(null),
    languageCode: Joi.string().required().label("Language"),groupName: Joi.string().required().label("Group Name"),keyName: Joi.string().required().label("Key"),valueText: Joi.string().label("Value Text"),description: Joi.string().label("Description"),
  };
*/
  async populateData() {
    try {
      const thisContext = this;

      // const id = this.props.match.params.id;
      const id =
        this.props.location.pathname.split("/")[
          this.props.location.pathname.split("/").length - 1
        ];
      if (id === undefined || id === "new") {
        return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    try {
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }

      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      languageCode: obj.languageCode,
      groupName: obj.groupName,
      keyName: obj.keyName,
      valueText: obj.valueText,
      description: obj.description,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
      if (
        response.data.status == "FAILED" ||
        response.data.validationMessages != undefined
      ) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages,
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message,
          });
        }
      } else {
        console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }
    });
  };

  htmlContent() {
    let thisContext = this;
    const { data, errors } = this.state;
    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
                <Card className="bg-light text-black">
                  <Card.Title className="card-form-title-dark">
                    {this.state.translations.titleLabel}
                  </Card.Title>
                  {/*<Card.Header>
				<p>Hello Translations please fillup your form!</p>
			</Card.Header>*/}
                  <Card.Body>
                    {/* form>(div.form-group>label+input.form-control)*2 */}
                    <div>
                      {this.renderInput(false, false, "id", "Id", "hidden")}
                      {this.renderInput(
                        true,
                        true,
                        "languageCode",
                        this.state.translations.languageCodeLabel,
                        this.state.translations.languageCodeLabel,
                        "text",
                        (data.id != null),
                        this.state.validationMessage.languageCode
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "groupName",
                        this.state.translations.groupNameLabel,
                        this.state.translations.groupNameLabel,
                        "text",
                        (data.id != null),
                        this.state.validationMessage.groupName
                      )}
                      {this.renderInput(
                        true,
                        true,
                        "keyName",
                        this.state.translations.keyNameLabel,
                        this.state.translations.keyNameLabel,
                        "text",
                        (data.id != null),
                        this.state.validationMessage.keyName
                      )}
                      {this.renderInput(
                        false,
                        true,
                        "valueText",
                        this.state.translations.valueTextLabel,
                        this.state.translations.valueTextLabel,
                        "text",
                        false,
                        this.state.validationMessage.valueText
                      )}
                      {this.renderInput(
                        false,
                        true,
                        "description",
                        this.state.translations.descriptionLabel,
                        this.state.translations.descriptionLabel,
                        "textarea",
                        false,
                        this.state.validationMessage.description
                      )}

                      {/* {this.renderInput("dueDate", "Due Date")} */}
                    </div>
                  </Card.Body>
                  <Card.Footer>
                    <div className="form-group row">
                      <div className="text-left col-md-4">
                        {this.renderCancelButton("Cancel", function (e) {
                          e.preventDefault();
                          thisContext.props.history.push(
                            "/admin/translationss"
                          );
                        })}
                      </div>
                      <div className="text-right col-md-4">
                        <span style={{ color: "red" }}>
                          {thisContext.state.commonValidationMessage}
                        </span>
                      </div>
                      <div className="text-right col-md-4">
                        {this.renderButton("Save", this.handleSubmit)}
                      </div>
                    </div>
                  </Card.Footer>
                </Card>
              </form>
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default TranslationsForm;
