import http from "../common/httpService";
import config from "../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/websiteFeatures";

function websiteFeatureUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}
export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(websiteFeatureId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(websiteFeatureUrl(websiteFeatureId), options);
}


export function save(datObj) {
	return datObj.name != null ? update(datObj) : create(datObj);
}


export function create(websiteFeature) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, websiteFeature, options);
}

export function update(websiteFeature) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, websiteFeature, options);
}

export function remove(websiteFeatureId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(websiteFeatureUrl(websiteFeatureId), options);
}

