import http from "../common/httpService";
import config from "../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/userMnoCsraPortals";

function userMnoCsraPortalUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}
export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(userMnoCsraPortalId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(userMnoCsraPortalUrl(userMnoCsraPortalId), options);
}

export function save(datObj) {
	return datObj.name != null ? update(datObj) : create(datObj);
}

export function create(userMnoCsraPortal) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, userMnoCsraPortal, options);
}

export function update(userMnoCsraPortal) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, userMnoCsraPortal, options);
}

export function remove(userMnoCsraPortalId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(userMnoCsraPortalUrl(userMnoCsraPortalId), options);
}

