import React, { Component, useState } from "react";
import AccordionTest from "./AccordionTest";
import AlertTest from "./AlertTest";
import ModalTest from "./ModalTest";
import ToastsTest from "./ToastsTest";

class ReactBootstrapManager extends Component {
  state = {};
  render() {
    let reactBootstrapComponent = (
      <div>
        <ToastsTest />
        <AccordionTest />
        <AlertTest />
        <ModalTest />
      </div>
    );
    return <div>{reactBootstrapComponent}</div>;
  }
}
export default ReactBootstrapManager;
