import ProductManager from "./components/product/productManager";
import ProductForm from "./components/product/productForm";
import SupplierManager from "./components/supplier/supplierManager";
import SupplierForm from "./components/supplier/supplierForm";
import VariantValuesManager from "./components/variantValues/variantValuesManager";
import VariantValuesForm from "./components/variantValues/variantValuesForm";
import SubCategoryManager from "./components/subCategory/subCategoryManager";
import SubCategoryForm from "./components/subCategory/subCategoryForm";
import ProductVariantManager from "./components/productVariant/productVariantManager";
import ProductVariantForm from "./components/productVariant/productVariantForm";
import RelatedProductManager from "./components/relatedProduct/relatedProductManager";
import RelatedProductForm from "./components/relatedProduct/relatedProductForm";
import ProductPriceManager from "./components/productPrice/productPriceManager";
import ProductPriceForm from "./components/productPrice/productPriceForm";
import ProductRatingManager from "./components/productRating/productRatingManager";
import ProductRatingForm from "./components/productRating/productRatingForm";
