import React from "react";
import { Accordion, Nav, Collapse } from "react-bootstrap";
import { Link, NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown, faCaretUp } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";
import {
  faHome,
  faBriefcase,
  faPaperPlane,
  faQuestion,
  faCopy,
  faImage,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";

class SubMenuUserManager extends React.Component {
  constructor(props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
    };
  }

  toggleNavbar() {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  render() {
    const { icon, title, items, translations, userPermissions } = this.props;

    return (
      <Nav.Item className={classNames({ open: !this.state.collapsed })}>
        <Accordion>
          <Accordion.Toggle
            as={Nav.Link}
            variant="link"
            eventKey="0"
            onClick={this.toggleNavbar}
          >
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {title}
            <FontAwesomeIcon
              icon={this.state.collapsed ? faCaretDown : faCaretUp}
              className="float-right"
            />
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="0">
            <nav className="nav flex-column">
              {/* <Nav.Item>
              <Nav.Link as={"div"}>
                <NavLink to={"/admin/userRequests"} className="nav-link nav-item">
                  <FontAwesomeIcon icon={faCopy} className="mr-2" />
                  User Request
                </NavLink>
                </Nav.Link>
              </Nav.Item> */}

              {/* {userPermissions.includes("USER_REQUEST_READ") && (
                <Nav.Item as={"li"}>
                  <Nav.Link as={"div"}>
                    <NavLink to="/admin/userRequests" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      {translations.userRequestMenuLabel}
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )} */}

              {userPermissions.includes("USER_READ") && (
                <Nav.Item as={"li"}>
                  <Nav.Link as={"div"}>
                    <NavLink to="/admin/users" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      {translations.userMenuLabel}
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}


              {userPermissions.includes("AUTHORITY_READ") && (
                <Nav.Item as={"li"}>
                  <Nav.Link as={"div"}>
                    <NavLink to="/admin/authoritys" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      {translations.authorityMenuLabel}
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

              {userPermissions.includes("PERMISSION_GROUP_READ") && (
                <Nav.Item as={"li"}>
                  <Nav.Link as={"div"}>
                    <NavLink
                      to="/admin/permissionGroups"
                      className="nav-link nav-item"
                    >
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      {translations.permissionGroupMenuLabel}
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

              {userPermissions.includes("APP_PERMISSION_READ") && (
                <Nav.Item as={"li"}>
                  <Nav.Link as={"div"}>
                    <NavLink
                      to="/admin/appPermissions"
                      className="nav-link nav-item"
                    >
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      {translations.appPermissionMenuLabel}
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

            </nav>
          </Accordion.Collapse>
        </Accordion>
      </Nav.Item>
    );
  }
}

export default SubMenuUserManager;
