import http from "../common/httpService";
import config from "../../config.json";

// import { reject } from "lodash";

const apiEndpoint = config.apiUrl + "/variantValuess";

function variantValuesUrl(id) {
	return `${apiEndpoint}/${id}`;
}

export function getAllPaginated(page = 0, size = 5) {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "?page=" + page + "&size=" + size, headerOptions);
}
export function getAll() {
	let headerOptions = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(apiEndpoint + "/all", headerOptions);
}

export function get(variantValuesId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.get(variantValuesUrl(variantValuesId), options);
}


export function save(datObj) {
	return datObj.name != null ? update(datObj) : create(datObj);
}


export function create(variantValues) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.post(apiEndpoint, variantValues, options);
}

export function update(variantValues) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};

	return http.put(apiEndpoint, variantValues, options);
}

export function remove(variantValuesId) {
	const options = {
		headers: { Authorization: "Bearer " + localStorage.getItem("token") },
	};
	return http.delete(variantValuesUrl(variantValuesId), options);
}

