import React, { Component } from "react";
import Table from "../common/table";
import { Button, Form, Modal } from 'react-bootstrap';
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faPencilAlt,
  faCheck
} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class MnoCsraPortalViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" size="sm" onClick={this.props.onClick}>
          <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form>
              <div className="form-group row">
                <div className="col-md-6">
                  <b><label>Mno Portal Title</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.mnoPortalTitle}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Csra Portal Default Url</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.csraPortalDefaultUrl}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Mno Portal Logo Base</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.mnoPortalLogoBase64}
                </div>
              </div><div className="form-group row">
                <div className="col-md-6">
                  <b><label>Mno Portal Requesting Url</label></b>
                </div>
                <div className="col-md-6">
                  {this.props.data.mnoPortalRequestingUrl}
                </div>
              </div>

              {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
            </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class MnoCsraPortalTable extends UxCrudTable {
  state = {
    userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    title: "Mno Csra Portal Details",
    translations: {
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      mnoPortalTitleLabel: "MNO Portal Title",
      csraPortalDefaultUrlLabel: "CSRA Portal Default Title",
      mnoPortalRequestingUrlLabel: "MNO Portal Requesting URL",
    },
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,

          titleLabel: translationsObj[
            "i18n.mnoCsraPortal.titleLabel"
          ]
            ? translationsObj["i18n.mnoCsraPortal.titleLabel"]
            : thisContext.state.translations.titleLabel,

          mnoPortalTitleLabel: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalTitle.label"
          ]
            ? translationsObj["i18n.mnoCsraPortal.mnoPortalTitle.label"]
            : thisContext.state.translations.mnoPortalTitleLabel,

          csraPortalDefaultUrlLabel: translationsObj[
            "i18n.mnoCsraPortal.csraPortalDefaultUrl.label"
          ]
            ? translationsObj[
            "i18n.mnoCsraPortal.csraPortalDefaultUrl.label"
            ]
            : thisContext.state.translations.csraPortalDefaultUrlLabel,
          mnoPortalRequestingUrlLabel: translationsObj[
            "i18n.mnoCsraPortal.mnoPortalRequestingUrl.label"
          ]
            ? translationsObj[
            "i18n.mnoCsraPortal.mnoPortalRequestingUrl.label"
            ]
            : thisContext.state.translations.mnoPortalRequestingUrlLabel,

        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  async loadColumns() {
    const thisContext = this;

    let columns = [
      { path: "id", label: "Id" },
      {
        path: "mnoPortalTitle",
        label: this.state.translations.mnoPortalTitleLabel,
      }, {
        path: "csraPortalDefaultUrl",
        label: this.state.translations.csraPortalDefaultUrlLabel,
      }, {
        path: "mnoPortalRequestingUrl",
        label: this.state.translations.mnoPortalRequestingUrlLabel,
      },
      {
        key: "view",
        label: this.state.translations.viewLabel,
        content: (item) => (
          <MnoCsraPortalViewModal
            itemId={this.state.itemId}
            show={this.state.showView}
            title={this.state.title}
            body={this.state.body}
            data={this.state.data}
            onClick={() => this.handleViewShow(item)}
            onHide={this.handleViewClose}
          />
        ),
      },
    ];

    if (thisContext.state.userPermissions.includes("MNO_CSRA_PORTAL_UPDATE")) {
      columns.push({
        key: "edit",
        label: this.state.translations.editLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onEdit(item)}
            className="btn btn-sm btn-info"
          >
            <FontAwesomeIcon icon={faPencilAlt} />
          </button>
        ),
      });
    }

    if (thisContext.state.userPermissions.includes("MNO_CSRA_PORTAL_APPROVE")) {
      columns.push({
        key: "approve",
        label: this.state.translations.approveLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onApprove(item)}
            className="btn btn-sm btn-success"
            disabled={item.status == "APPROVED"}
          >
            <FontAwesomeIcon icon={faCheck} />
          </button>
        ),
      });
    }

    if (thisContext.state.userPermissions.includes("MNO_CSRA_PORTAL_DELETE")) {
      columns.push({
        key: "delete",
        label: this.state.translations.deleteLabel,
        content: (item) => (
          <DeleteConfirmModal
            disabled={item.status == "APPROVED"}
            show={this.state.showDeleteConfirm}
            item={this.state.item}
            onClick={() => this.handleDeleteModalShow(item)}
            onHide={this.handleDeleteConfirmClose}
            onConfirm={(item) => this.handleDeleteFromModal(item)}
          />
        ),
      });
    }

    this.setState({ columns: columns });
  }

}

export default MnoCsraPortalTable;
