import React from "react";
import { Link, NavLink } from "react-router-dom";
import { Nav, Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import logo from "../../images/logo.jpg";

import {
  faHome,
  faBriefcase,
  faImage,
} from "@fortawesome/free-solid-svg-icons";

import SubMenuSettings from "./SubMenuSettings";
import classNames from "classnames";
import SubMenuUserManager from "./SubMenuUserManager";

class AdminPanelSideBar extends React.Component {
  state = {
    activeLanguage: this.props.activeLanguage,
    userPermissions: [],
    isLoggedIn: false,
    isLoading: true,
    loginUserEmail: "",
    loginUserFirstName: "",
    loginUserLastName: "",
    translations: {
      loginUserHeaderLabel: "Login User:",
      dashboardMenuLabel: "Dashboard",
      dataStatusMenuLabel: "Data Status",
      securityStatusMenuLabel: "Security Status",
      userManagerMenuLabel: "User Manager",
      adminPanelMenuLabel: "Admin Panel",
      // userRequestMenuLabel: "User Request",
      userMenuLabel: "Users",
      translationsMenuLabel: "Translations",
      authorityMenuLabel: "Authority",
      permissionGroupMenuLabel: "Permission Group",
      appPermissionMenuLabel: "App Permission",
      userMnoCsraPortalMenuLabel: "User Mno CSRA Portal",
      mnoCsraPortalMenuLabel: "Mno CSRA Portal",
      mnoCsraAuthorizationKeyMenuLabel: "Mno Csra Authorization Key",
      mnoMenuLabel: "MNO",
    },
  };

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  };

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          loginUserHeaderLabel: translationsObj[
            "i18n.menu.loginUserHeaderLabel"
          ]
            ? translationsObj["i18n.menu.loginUserHeaderLabel"]
            : thisContext.state.translations.loginUserHeaderLabel,

          dashboardMenuLabel: translationsObj["i18n.menu.dashboardMenuLabel"]
            ? translationsObj["i18n.menu.dashboardMenuLabel"]
            : thisContext.state.translations.dashboardMenuLabel,

          dataStatusMenuLabel: translationsObj[
            "i18n.menu.dataStatusMenuLabel"
          ]
            ? translationsObj["i18n.menu.dataStatusMenuLabel"]
            : thisContext.state.translations.dataStatusMenuLabel,

          securityStatusMenuLabel: translationsObj[
            "i18n.menu.securityStatusLabel"
          ]
            ? translationsObj["i18n.menu.securityStatusLabel"]
            : thisContext.state.translations.securityStatusMenuLabel,

          adminPanelMenuLabel: translationsObj["i18n.menu.adminPanelLabel"]
            ? translationsObj["i18n.menu.adminPanelLabel"]
            : thisContext.state.translations.adminPanelMenuLabel,

          userManagerMenuLabel: translationsObj["i18n.menu.userManagerMenuLabel"]
            ? translationsObj["i18n.menu.userManagerMenuLabel"]
            : thisContext.state.translations.userManagerMenuLabel,

          // userRequestMenuLabel: translationsObj["i18n.menu.userRequestMenuLabel"]
          //   ? translationsObj["i18n.menu.userRequestMenuLabel"]
          //   : thisContext.state.translations.userRequestMenuLabel,

          userMenuLabel: translationsObj["i18n.menu.userMenuLabel"]
            ? translationsObj["i18n.menu.userMenuLabel"]
            : thisContext.state.translations.userMenuLabel,

          translationsMenuLabel: translationsObj[
            "i18n.menu.translationsMenuLabel"
          ]
            ? translationsObj["i18n.menu.translationsMenuLabel"]
            : thisContext.state.translations.translationsMenuLabel,

          authorityMenuLabel: translationsObj["i18n.menu.authorityMenuLabel"]
            ? translationsObj["i18n.menu.authorityMenuLabel"]
            : thisContext.state.translations.authorityMenuLabel,

          permissionGroupMenuLabel: translationsObj[
            "i18n.menu.permissionGroupMenuLabel"
          ]
            ? translationsObj["i18n.menu.permissionGroupMenuLabel"]
            : thisContext.state.translations.permissionGroupMenuLabel,

          appPermissionMenuLabel: translationsObj[
            "i18n.menu.appPermissionMenuLabel"
          ]
            ? translationsObj["i18n.menu.appPermissionMenuLabel"]
            : thisContext.state.translations.appPermissionMenuLabel,

          userMnoCsraPortalMenuLabel: translationsObj[
            "i18n.menu.userMnoCsraPortalMenuLabel"
          ]
            ? translationsObj["i18n.menu.userMnoCsraPortalMenuLabel"]
            : thisContext.state.translations.userMnoCsraPortalMenuLabel,

          mnoCsraPortalMenuLabel: translationsObj[
            "i18n.menu.mnoCsraPortalMenuLabel"
          ]
            ? translationsObj["i18n.menu.mnoCsraPortalMenuLabel"]
            : thisContext.state.translations.mnoCsraPortalMenuLabel,

          mnoMenuLabel: translationsObj["i18n.menu.mnoMenuLabel"]
            ? translationsObj["i18n.menu.mnoMenuLabel"]
            : thisContext.state.translations.mnoMenuLabel,

          mnoCsraAuthorizationKeyMenuLabel: translationsObj[
            "i18n.menu.mnoCsraAuthorizationKeyMenuLabel"
          ]
            ? translationsObj["i18n.menu.mnoCsraAuthorizationKeyMenuLabel"]
            : thisContext.state.translations.mnoCsraAuthorizationKeyMenuLabel,
        },
      });

      // console.log("AdminPanelSideBar :: loadTranslations");
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  reloadTranslations = (updatedLanguage) => {
    // console.log("+++ AdminPanelSideBar :: reloadTranslations +++");
    // console.log("updatedLanguage: " + updatedLanguage);

    try {
      this.loadTranslations();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }

    // console.log("--- AdminPanelSideBar :: reloadTranslations ---");
  };

  async componentDidMount() {
    const thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    try {
      await this.loadTranslations();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
      this.setState({ isLoading: false }); // Set loading state to false in case of error
    }
    let user = localStorage.getItem("user");
    if (user != null && user != undefined) {
      let userObj = JSON.parse(user);
      thisContext.setState({
        loginUserEmail: userObj.email,
        loginUserFirstName: userObj.firstName,
        loginUserLastName: userObj.lastName,
      });
    }
  }
  render() {
    const customFontStyle = {
      fontFamily: "Diodrum Arabic Semibold, sans-serif",
      fontWeight: "5",
    };
    return (
      <div
        className={classNames(
          { "sidebar-rtl": this.props.isRtl },
          { sidebar: !this.props.isRtl },
          { "is-open": this.props.isOpen }
        )}
      >
        <div className="sidebar-header">
          <Button
            variant="link"
            onClick={this.props.toggle}
            style={{ color: "#fff" }}
            className="mt-4"
          >
            <FontAwesomeIcon icon={faBriefcase} pull="right" size="xs" />
          </Button>
          {/* <h3><img src="https://upload.wikimedia.org/wikipedia/en/d/de/Saudi_arabia_railways_logo.jpg" width={"180px"} height={"70px"} /></h3> */}
          <h3>
            <img src={logo} width={"180px"} height={"120px"} />
          </h3>
        </div>

        <Nav className="flex-column pt-2">
          <p className="ml-3">
            {this.state.translations.loginUserHeaderLabel}
            {this.state.loginUserFirstName} {this.state.loginUserLastName}
          </p>

          <Nav.Item as={"li"}>
            <NavLink to="/admin/dashboard" className="nav-link nav-item">
              <FontAwesomeIcon icon={faHome} className="mr-2" />
              {this.state.translations.dashboardMenuLabel}
            </NavLink>
          </Nav.Item>

          {this.state.userPermissions.includes("APP_STATUS_READ") && (
            <>
              <Nav.Item as={"li"}>
                <NavLink to="/admin/appStatus" className="nav-link nav-item">
                  <FontAwesomeIcon icon={faHome} className="mr-2" />
                  {this.state.translations.dataStatusMenuLabel}
                </NavLink>
              </Nav.Item>
              {/* <Nav.Item as={"li"}>
                <NavLink
                  to="/admin/securityStatus"
                  className="nav-link nav-item"
                >
                  <FontAwesomeIcon icon={faHome} className="mr-2" />
                  {this.state.translations.securityStatusMenuLabel}
                </NavLink>
              </Nav.Item> */}
            </>
          )}


          {/* {this.state.userPermissions.includes("USER_MANAGER_READ") && ( */}
          {this.state.userPermissions.includes("APP_SETTINGS_READ") && (
            <SubMenuSettings userPermissions={this.state.userPermissions}
              translations={this.state.translations}
              title={this.state.translations.adminPanelMenuLabel} />
          )}

          {this.state.userPermissions.includes("USER_READ") && (
            <SubMenuUserManager userPermissions={this.state.userPermissions}
              translations={this.state.translations}
              title={this.state.translations.userManagerMenuLabel} />
          )}
          {/* Organization
          Organization Type
          Employee
          Designation */}

{this.state.userPermissions.includes("CATEGORY_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/categorys" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Category
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("SUB_CATEGORY_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/subCategorys" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Sub Category
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("RELATED_PRODUCT_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/relatedProducts" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Related Product
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("PRODUCT_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/products" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Product
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("PRODUCT_RATING_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/productRatings" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Product Rating
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("SUPPLIER_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/suppliers" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Supplier
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("BRAND_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/brands" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Brand
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("WEBSITE_FEATURE_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/websiteFeatures" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Website Feature
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}

{this.state.userPermissions.includes("WEBSITE_INFO_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/websiteInfos" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Website Info
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("CUSTOMER_REVIEW_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/customerReviews" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Customer Review
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{this.state.userPermissions.includes("SUPPORTED_PAYMENT_GATEWAY_READ") && (
<Nav.Item>
            <Nav.Link>
              <NavLink to="/admin/supportedPaymentGateways" className="nav-link nav-item">
                <FontAwesomeIcon icon={faImage} className="mr-2" />
                Supported Payment Gateway
              </NavLink>
            </Nav.Link>
          </Nav.Item>
)}
{/*newNavLinks*/}

        </Nav>
      </div>
    );
  }
}

export default AdminPanelSideBar;
