import React from "react";
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faAlignLeft } from "@fortawesome/free-solid-svg-icons";
import { Navbar, Container, Row, Col } from "react-bootstrap";
// import { Link, NavLink } from "react-router-dom";
import styled from "styled-components";
import vendorLogo from "../../../images/vendor.png";
import properties from '../../../common_properties.json';

// import ShoppingCart from '../../sidebar/ShoppingCart';
// import { checkAuthentication } from "../../common/authenticationService";

const Styles = styled.div`
  .navbar {
    background-color: skyblue;
    color: black;
  }
  a,
  .navbar-brand,
  .navbar-nav .nav-link {
    color: black;
    &:hover {
      color: gray;
      background-color: white;
    }
  }
  body {
    paddingTop: 70px;
  }
`;
class PublicFooter extends React.Component {
  state = {
    totalCartItemPrice: 0,
    cartProducts: [],
    translations: {
      footerMessageCopyright: "Copyright",
      footerMessagePoweredByLabel: "Powered by",
      footerMessageAllRightsReservedLabel: "All rights reserved"
    }
  };

  async componentDidMount() {
    await this.loadTranslations();
  }

  reloadTranslations = (updatedLanguage) => {
    this.loadTranslations();
  };

  hideAlertModal() {
    this.setState({
      alertShow: false,
      alertMessage: "",
      alertDetailsMessage: "",
    });
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          footerMessageCopyright: translationsObj["i18n.common.footerMessageCopyright"]
            ? translationsObj["i18n.common.footerMessageCopyright"]
            : thisContext.state.translations.footerMessageCopyright,
          footerMessagePoweredByLabel: translationsObj["i18n.common.footerMessagePoweredByLabel"]
            ? translationsObj["i18n.common.footerMessagePoweredByLabel"]
            : thisContext.state.translations.footerMessageCopyright,
          footerMessageAllRightsReservedLabel: translationsObj["i18n.common.footerMessageAllRightsReservedLabel"]
            ? translationsObj["i18n.common.footerMessageAllRightsReservedLabel"]
            : thisContext.state.translations.footerMessageAllRightsReservedLabel,
        }
      });
      // console.log("AdminPanelNavBar :: loadTranslations");
    } else {
      // console.log("Alert!!!, Translations not found!");
    }
  }

  render() {
    const { isLoggedIn } = this.props;
    return (
      <Styles>
        <footer className="footer" style={{ bottom: "10px", "paddingTop": "5px" , "background-color":"#E9ECEF" }}>
          <div className="container" style={{"paddingTop": "5px" }}>
            <div className="row">
              <div
                className="col-md-8"
                style={{
                  lineHeight: "50px",
                  display: "flex",
                  justifyContent: "left",
                }}
              >
                <p>
                  <b>
                    {this.state.translations.footerMessageCopyright} &copy; {new Date().getFullYear()}{" "}
                    <a target="_blank" href={properties.domainUrl}>{properties.labels.languages.en.app_name_details}</a>, {this.state.translations.footerMessageAllRightsReservedLabel}
                  </b>
                </p>
              </div>
              <div className="col-md-4" style={{
                lineHeight: "50px",
                display: "flex",
                justifyContent: "right",
              }}>
                <span className="pull-right">
                  <b>{this.state.translations.footerMessagePoweredByLabel} </b>
                  <a target="_blank" href={properties.vendorUrl}>
                    <img style={{ height: "15px" }} src={vendorLogo} />
                  </a>
                </span>
              </div>
            </div>
          </div>
        </footer>
      </Styles>
    );
  }
}

export default PublicFooter;
