import React, { useState, useEffect } from 'react';
import axios from 'axios';
import config from "../../config.json";
const userUpdateApiEndPoint =  config.apiUrl;

const Profile = () => {
  const [profile, setProfile] = useState({
    username: '',
    firstName: '',
    lastName: '',
    email: '',
    mobileNo:'',
    newPassword:'',
    oldPassword:'',
    confirmPassword: '',
  });
  const [response, setResponse] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchUserDetails = async () => {
      let token = localStorage.getItem("token");
      if (!token) {
        return;
      }

      try {
        const config = {
          headers: { Authorization: "Bearer " + token },
        };
        const res = await axios.get(userUpdateApiEndPoint + "/user", config);
        const userData = res.data;
        setProfile({
          username: userData.username || '',
          firstName: userData.firstName || '',
          lastName: userData.lastName || '',
          email: userData.email || '',
          mobileNo:  userData.mobileNo  || '',
          oldPassword: '',
          password: '',
          confirmPassword: '',

        });
      } catch (err) {
        console.error("Error fetching user details:", err);
      }
    };

    fetchUserDetails();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProfile({
      ...profile,
      [name]: value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const newPassword = profile.password ? profile.password.trim() : '';
    const confirmNewPassword = profile.confirmPassword ? profile.confirmPassword.trim() : '';

 
    // Check if newPassword or confirmNewPassword is empty
  
      if (newPassword !== confirmNewPassword) {
        setError("New password and confirm password do not match");
        return;
      }
    

    // Check if newPassword matches confirmNewPassword
 

    if (!newPassword ) {
      profile.password = profile.oldPassword
    }

    try {
      let token = localStorage.getItem("token");
      const res = await axios.post(userUpdateApiEndPoint + '/profiles', profile, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: "Bearer " + token,
        },
      });
      setResponse(res.data);
      setError(null);
    } catch (err) {
      setError( 'Confirm Your Password');
      setResponse(null);
    }
  };

  return (
    <div className="container mt-5">
      <h1 className="mb-6">Update Profile</h1>
      <form onSubmit={handleSubmit}>
        <div>
          <label>Username:</label>
          <input
            type="text"
            className="form-control"
            name="username"
            value={profile.username}
            onChange={handleChange}
            readOnly
          />
        </div>
        <div>
          <label>First Name:</label>
          <input
            type="text"
            className="form-control"
            name="firstName"
            value={profile.firstName}
            onChange={handleChange}
          />
        </div>
        <div>
          <label>Last Name:</label>
          <input
            type="text"
            className="form-control"
            name="lastName"
            value={profile.lastName}
            onChange={handleChange}
          />
        </div>
        <div>
          <label>Email:</label>
          <input
            type="email"
            className="form-control"
            name="email"
            value={profile.email}
            onChange={handleChange}
          />
        </div>
        <div>
          <label>Mobile No:</label>
          <input
            type="text"
            className="form-control"
            name="mobileNo"
            value={profile.mobileNo}
            onChange={handleChange}
          />
        </div>
        
        <div>
          <label>Old Password:<i className="star-requird">*</i></label>
          <input
            type="password"
            className="form-control"
            name="oldPassword"
            value={profile.oldPassword}
            onChange={handleChange}
            required
          />
        </div>

        <div>
          <label>New Password:</label>
          <input
            type="password"
            className="form-control"
            name="password"
            value={profile.password}
            onChange={handleChange}
          />
        </div>
        <div>
          <label>Confirm New Password:</label>
          <input
            type="password"
            className="form-control"
            name="confirmPassword"
            value={profile.confirmPassword}
            onChange={handleChange}
          />
        </div>
        <button type="submit" className="btn btn-primary">Update Profile</button>
      </form>
      {response && (
        <div>
       <p> Profile Updated Successfully! </p>
        </div>
      )}
      {error && (
        <div>
          <h2>Error</h2>
          <pre>{error}</pre>
        </div>
      )}
    </div>
  );
};

export default Profile;
