import React from "react";
import UxCrudForm from "../common/UxCrudForm";
import { save, get } from "./supplierService";
// import Joi from "joi-browser";

import { Container, Row, Col, Card } from "react-bootstrap";
import FileBase64 from 'react-file-base64';


class SupplierForm extends UxCrudForm {
  state = {
	isLoading: true,
    requiredPermissions: ["SUPPLIER_CREATE","SUPPLIER_UPDATE"],
    moduleUrl: "/admin",
    basicUrl: "/admin/suppliers/",
	userPermissions: [],
    data: { id: null , name: "", streetAddress: "", contactNumber: "", email: "", remarks: "" },
    errors: {},
    validationMessage: {
      name: '',
streetAddress: '',
contactNumber: '',
email: '',

    },
	commonValidationMessage:"",
	translations: {
    	titleLabel: "Supplier",
	    homeLabel:"Home",
	    cancelLabel: "Cancel",
	    saveLabel: "Save",
      	// orgNameLabel: "Organization Name",
      	nameLabel: "Name",
namePlaceholder: "Enter Name",
streetAddressLabel: "Street Address",
streetAddressPlaceholder: "Enter Street Address",
contactNumberLabel: "Contact Number",
contactNumberPlaceholder: "Enter Contact Number",
emailLabel: "Email",
emailPlaceholder: "Enter Email",
remarksLabel: "Remarks",
remarksPlaceholder: "Enter Remarks",

    },
	  
  };
  
 
/* schema = {
    id: Joi.number().allow(null),
    name: Joi.string().required().label({this.state.translations.nameLabel}),streetAddress: Joi.string().required().label({this.state.translations.streetAddressLabel}),contactNumber: Joi.string().required().label({this.state.translations.contactNumberLabel}),email: Joi.string().required().label({this.state.translations.emailLabel}),remarks: Joi.string().label({this.state.translations.remarksLabel}),
  };
*/
  async populateData() {
    try { 
      const thisContext = this;
      
      // const id = this.props.match.params.id;
      const id = this.props.location.pathname.split("/")[this.props.location.pathname.split("/").length-1];
      if (id === undefined || id === 'new') {
    	  return;
      }

      const { data: obj } = await get(id);
      // console.log(obj);
      this.setState({ data: this.mapToViewModel(obj) });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        this.props.history.replace("/not-found");
    }
  }

  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
		  cancelLabel: translationsObj["i18n.common.cancelLabel"] ? translationsObj["i18n.common.cancelLabel"] : thisContext.state.translations.cancelLabel,
		  saveLabel: translationsObj["i18n.common.saveLabel"] ? translationsObj["i18n.common.saveLabel"] : thisContext.state.translations.saveLabel,
		  homeLabel: translationsObj["i18n.common.homeLabel"] ? translationsObj["i18n.common.homeLabel"] : thisContext.state.translations.homeLabel,
		  titleLabel: translationsObj["i18n.supplier.titleLabel"] ? translationsObj["i18n.supplier.titleLabel"] : thisContext.state.translations.titleLabel,
          // orgNameLabel: translationsObj["hr.organization.orgName.label"] ? translationsObj["hr.organization.orgName.label"] : thisContext.translations.orgNameLabel,
          nameLabel: translationsObj["i18n.supplier.name.label"] ? translationsObj["i18n.supplier.name.label"] : thisContext.state.translations.nameLabel,
namePlaceholder: translationsObj["i18n.supplier.name.placeholder"] ? translationsObj["i18n.supplier.name.placeholder"] : thisContext.state.translations.namePlaceholder,
streetAddressLabel: translationsObj["i18n.supplier.streetAddress.label"] ? translationsObj["i18n.supplier.streetAddress.label"] : thisContext.state.translations.streetAddressLabel,
streetAddressPlaceholder: translationsObj["i18n.supplier.streetAddress.placeholder"] ? translationsObj["i18n.supplier.streetAddress.placeholder"] : thisContext.state.translations.streetAddressPlaceholder,
contactNumberLabel: translationsObj["i18n.supplier.contactNumber.label"] ? translationsObj["i18n.supplier.contactNumber.label"] : thisContext.state.translations.contactNumberLabel,
contactNumberPlaceholder: translationsObj["i18n.supplier.contactNumber.placeholder"] ? translationsObj["i18n.supplier.contactNumber.placeholder"] : thisContext.state.translations.contactNumberPlaceholder,
emailLabel: translationsObj["i18n.supplier.email.label"] ? translationsObj["i18n.supplier.email.label"] : thisContext.state.translations.emailLabel,
emailPlaceholder: translationsObj["i18n.supplier.email.placeholder"] ? translationsObj["i18n.supplier.email.placeholder"] : thisContext.state.translations.emailPlaceholder,
remarksLabel: translationsObj["i18n.supplier.remarks.label"] ? translationsObj["i18n.supplier.remarks.label"] : thisContext.state.translations.remarksLabel,
remarksPlaceholder: translationsObj["i18n.supplier.remarks.placeholder"] ? translationsObj["i18n.supplier.remarks.placeholder"] : thisContext.state.translations.remarksPlaceholder,

        }
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

  loadPermissions = () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        let permissions = localStorage.getItem("permissions");
        const userPermissions = permissions.split(",");
        resolve(userPermissions);
      }, 100);
    });
  }

   async componentDidMount() {
    let thisContext = this;
    const permissions = await thisContext.loadPermissions();
    thisContext.setState({ userPermissions: permissions });
    thisContext.setState({ isLoading: true }); // Set loading state to true before API calls

    if (
      !(thisContext.state.userPermissions.includes("SUPPLIER_CREATE") ||
      thisContext.state.userPermissions.includes("SUPPLIER_UPDATE"))
    ) {
      thisContext.props.history.push(thisContext.state.basicUrl);
      return;
    }
  
    try {
	  await this.loadTranslations();
      await this.populateData();
      this.setState({ isLoading: false }); // Set loading state to false after API calls
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        this.props.history.replace("/not-found");
      }
  
      this.setState({ loading: false }); // Set loading state to false in case of error
    }
  }

  mapToViewModel(obj) {
    return {
      id: obj.id,
      name: obj.name,streetAddress: obj.streetAddress,contactNumber: obj.contactNumber,email: obj.email,remarks: obj.remarks,
    };
  }

  doSubmit = async () => {
    let thisContext = this;
    await save(thisContext.state.data).then(function (response) {
	if (response.data.status == "FAILED" || response.data.validationMessages != undefined) {
        if (response.data.validationMessages != undefined) {
          thisContext.setState({
            validationMessage: response.data.validationMessages
          });
        } else if (response.data.message != undefined) {
          thisContext.setState({
            commonValidationMessage: response.data.message
          });
        }

      } else {
        // console.log("Save Successful.");
        thisContext.props.history.push(thisContext.state.basicUrl);
      }

    });
  };

  
  
  htmlContent() {
	let thisContext = this;
    const { data, errors} = this.state;

    return (
      <React.Fragment>
        <Container className="bg-light text-black">
          <Row>
            <Col lg="12" md="12">
              <form onSubmit={this.handleSubmit}>
            <Card className="bg-light text-black">
			<Card.Title className="card-form-title-dark">{this.state.translations.titleLabel}</Card.Title>
			{/*<Card.Header>
				<p>Hello Supplier please fillup your form!</p>
			</Card.Header>*/}
            <Card.Body>
              {/* form>(div.form-group>label+input.form-control)*2 */}
				<div>
                {this.renderInput(false,false,"id", "Id", "hidden")}
                {this.renderInput(true,true,"name", this.state.translations.nameLabel,this.state.translations.namePlaceholder  ,"text",false,this.state.validationMessage.name)}
{this.renderInput(true,true,"streetAddress", this.state.translations.streetAddressLabel,this.state.translations.streetAddressPlaceholder  ,"textarea",false,this.state.validationMessage.streetAddress)}
{this.renderInput(true,true,"contactNumber", this.state.translations.contactNumberLabel,this.state.translations.contactNumberPlaceholder  ,"text",false,this.state.validationMessage.contactNumber)}
{this.renderInput(true,true,"email", this.state.translations.emailLabel,this.state.translations.emailPlaceholder  ,"text",false,this.state.validationMessage.email)}
{this.renderInput(false,true,"remarks", this.state.translations.remarksLabel,this.state.translations.remarksPlaceholder  ,"textarea",false,this.state.validationMessage.remarks)}

                {/* {this.renderInput("dueDate", "Due Date")} */}
				</div>
                
            </Card.Body>
			<Card.Footer>
			  <div className="form-group row">
	                <div className="text-left col-md-4">
		                {
		            		this.renderCancelButton(thisContext.state.translations.cancelLabel,function(e){
		            			e.preventDefault();
		            			thisContext.props.history.push(thisContext.state.basicUrl);
		            		})
		            	}
	                </div>
					<div className="text-right col-md-4">
	                    <span style={{ color: 'red' }}>{thisContext.state.commonValidationMessage}</span>
	                </div>
	                <div className="text-right col-md-4">
	                		{this.renderButton(thisContext.state.translations.saveLabel, this.handleSubmit)}
	            	</div>
                
               </div>
			</Card.Footer>
          </Card>
              </form>
              
            </Col>
            <Col lg="auto" md="auto"></Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default SupplierForm;
