import React, { useState, useEffect } from "react";
import http from "../common/httpService";
import config from "../../config.json";
import { Col, Container, Jumbotron, Row, Spinner, Table } from "react-bootstrap";
const apiEndpointTranslationsImport = config.apiUrl + "/i18n/import";

function ExcelImportComponent() {
  const [userPermissions, setUserPermissions] = useState([]);
  const [groupName, setGroupName] = useState("");
  const [file, setFile] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [uploadedData, setUploadedData] = useState([]);

  const requiredPermission = "TRANSLATIONS_MANAGER";

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleInputChange = (e) => {
    if (e.target.name == "groupName") {
      setGroupName(e.target.value);
    }
  };

  const loadUserPermissions = () => {
    let permissions = localStorage.getItem("permissions");
    const permissionArr = permissions.split(",");
    setUserPermissions(permissionArr);
  };

  useEffect(() => {
    // setIsLoading(true);
    loadUserPermissions();
    setIsLoading(false);
  }, []);

  const handleUpload = async () => {
    if (!file) return;

    const formData = new FormData();
    formData.append("file", file);
    formData.append("groupName", groupName);

    try {
      const options = {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
          "Content-Type": "multipart/form-data",
        },
      };

      const response = await http.post(
        apiEndpointTranslationsImport,
        formData,
        options
      );

      const responseData = response.data;
      // console.log("File uploaded:", JSON.stringify(responseData.data));
      setUploadedData(responseData.data);
    } catch (error) {
      console.error("Error uploading file:", error);
    }
  };

  if (isLoading) {
    return (
      <Spinner animation="border" role="status">
        <span className="visually-hidden"></span>
      </Spinner>
    );
  } else if (userPermissions.includes(requiredPermission)) {
    return (
      <Jumbotron>
        <Container>
          <Row>
            <h3>Translations Import</h3>
          </Row>
        </Container>
        <Container>
          <Row>
            <div className="form-group">
              <label className="label" htmlFor="languageGroupName">
                Language Group Name
              </label>
              {/* <input
            className="input"
            type="text"
            id="groupName"
            name="groupName"
            placeholder={"Enter Group Name"}
            onChange={handleInputChange}
          /> */}
              <select name="groupName" id="groupName" onChange={handleInputChange}>
                <option value="">--select--</option>
                <option value="i18n">i18n</option>
                <option value="app">App</option>
              </select>
            </div>
            <div className="form-group">
              <label className="label" htmlFor="translationsFile">
                Choose Translations File
              </label>
              <input type="file" onChange={handleFileChange} />
            </div>
            <div className="form-group">
              <label className="label" htmlFor="uploadButton"></label>
              <button onClick={handleUpload}>Upload</button>
            </div>
          </Row>
          <Row>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>Sl/No</th>
                  <th>Language</th>
                  <th>Group Name</th>
                  <th>Key Name</th>
                  <th>Value Text</th>
                </tr>
              </thead>
              {uploadedData.map((item, index) => (
                <tr key={index + "-" + item.id}>
                  <td key={index + "-" + item.id}>{index+1}</td>
                  <td key={index + "-" + item.languageCode}>{item.languageCode}</td>
                  <td key={index + "-" + item.groupName}>{item.groupName}</td>
                  <td key={index + "-" + item.keyName}>{item.keyName}</td>
                  <td key={index + "-" + item.valueText}>{item.valueText}</td>
                </tr>
              ))}
            </Table>
          </Row>
        </Container>
      </Jumbotron>
    );
  } else {
    return <h4>You are not permitted to view this page</h4>;
  }
}

export default ExcelImportComponent;
