import React, { useState } from 'react';
import Select from 'react-select';
import { useTranslation } from "react-i18next";
import './DemoForm.css';

const DemoForm = () => {
  const { t, i18n } = useTranslation();
  const [language, setLanguage] = useState('en'); // Current language state

  const handleLanguageChange = (newLanguage) => {
    setLanguage(newLanguage);
    i18n.changeLanguage(newLanguage); // Update the language using i18n
  };

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [country, setCountry] = useState('');
  const [hobbies, setHobbies] = useState([]);
  const [salary, setSalary] = useState('');
  const [gender, setGender] = useState('');
  const [employeeType, setEmployeeType] = useState(false);

  const countryOptions = [
    { value: 'USA', label: 'USA' },
    { value: 'Canada', label: 'Canada' },
    { value: 'UK', label: 'UK' },
    { value: 'Australia', label: 'Australia' }
  ];

  const hobbiesOptions = [
    { value: 'Reading', label: 'Reading' },
    { value: 'Gaming', label: 'Gaming' },
    { value: 'Traveling', label: 'Traveling' },
    { value: 'Sports', label: 'Sports' }
  ];

  const handleCountryChange = (selectedOption) => {
    setCountry(selectedOption);
  };

  const handleHobbiesChange = (selectedOptions) => {
    setHobbies(selectedOptions);
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    // Submit form data
    // Replace this with your actual form submission logic
    console.log('Form submitted:', { name, email, phone, country, hobbies, salary, gender, employeeType });

    // Reset form fields
    setName('');
    setEmail('');
    setPhone('');
    setCountry('');
    setHobbies([]);
    setSalary('');
    setGender('');
    setEmployeeType(false);
  };

  return (
    <div className="basic-form light">
      <h4 className="form-title">{t('form.demoForm.title')}</h4>
      <hr />
      {/* Language switch buttons */}
      <div className="language-switch">
        <button onClick={() => handleLanguageChange('en')}>English</button>
        <button onClick={() => handleLanguageChange('bn')}></button>
      </div>

      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label className="label" htmlFor="name">
            {t('form.demoForm.name.label')}
          </label>
          <input
            className="input"
            type="text"
            placeholder={t('form.demoForm.name.placeholder')}
            id="name"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />

        </div>
        <div className="form-group">
          <label className="label" htmlFor="email">{t('form.demoForm.email.label')}</label>
          <input className="input" type="email" id="email" value={email} placeholder={t('form.demoForm.email.placeholder')} onChange={(e) => setEmail(e.target.value)} />
        </div>
        <div className="form-group">
          <label className="label" htmlFor="phone">{t('form.demoForm.phone.label')}</label>
          <input className="input" type="tel" id="phone" value={phone} placeholder={t('form.demoForm.phone.placeholder')} onChange={(e) => setPhone(e.target.value)} />
        </div>
        <div className="form-group">
          <label className="label" htmlFor="country">{t('form.demoForm.country.label')}</label>
          <Select
            id="country"
            options={countryOptions}
            value={country}
            onChange={handleCountryChange}
            placeholder={t('form.demoForm.country.placeholder')}
            className="react-select-container"
            classNamePrefix="react-select"
          />
        </div>
        <div className="form-group">
          <label className="label" htmlFor="hobbies">{t('form.demoForm.hobbies.label')}</label>
          <Select
            id="hobbies"
            options={hobbiesOptions}
            value={hobbies}
            onChange={handleHobbiesChange}
            isMulti
            placeholder={t('form.demoForm.hobbies.placeholder')}
            className="react-select-container"
            classNamePrefix="react-select"
          />
        </div>
        <div className="form-group">
          <label className="label" htmlFor="salary">{t('form.demoForm.salary.label')}</label>
          <input placeholder={t('form.demoForm.salary.placeholder')} className="input" type="number" id="salary" value={salary} onChange={(e) => setSalary(e.target.value)} />
        </div>
        <div className="form-group">
          <label className="label" htmlFor="gender">{t('form.demoForm.gender.label')}</label>
          <div>
            <input type="radio" id="gender-male" name="gender" value="male" checked={gender === 'male'} onChange={(e) => setGender(e.target.value)} />
            <label className="label" htmlFor="gender-male">{t('form.demoForm.genderMale.label')}</label>
          </div>
          <div>
            <input type="radio" id="gender-female" name="gender" value="female" checked={gender === 'female'} onChange={(e) => setGender(e.target.value)} />
            <label className="label" htmlFor="gender-female">{t('form.demoForm.genderFemale.label')} </label>
          </div>
          <div>
            <input type="radio" id="gender-other" name="gender" value="other" checked={gender === 'other'} onChange={(e) => setGender(e.target.value)} />
            <label className="label" htmlFor="gender-other">{t('form.demoForm.genderOther.label')}</label>
          </div>
        </div>
        <div className="form-group">
          <input className="input" type="checkbox" id="employeeType" checked={employeeType} onChange={(e) => setEmployeeType(e.target.checked)} />
          <label className="label" htmlFor="employeeType">{t('form.demoForm.fullTimeEmployee.label')}</label>
        </div>
        <button type="submit" className="submit-btn">{t('form.demoForm.submitButton.label')}</button>
      </form>
    </div>
  );
};

export default DemoForm;
