import React, { Component } from "react";
import Table from "../common/table";
import {Button, Form, Modal} from 'react-bootstrap';
import DeleteConfirmModal from "../common/DeleteConfirmModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	  faEye,
	  faPencilAlt,
	  faCheck
	} from "@fortawesome/free-solid-svg-icons";
import UxCrudTable from "../common/UxCrudTable";

class WebsiteInfoViewModal extends Component {
  render() {
    return (
      <div>
        <Button variant="primary" onClick={this.props.onClick}>
        <FontAwesomeIcon icon={faEye} />
        </Button>
        <Modal
          show={this.props.show}
          onHide={() => this.props.onHide({ msg: "Cross Icon Clicked!" })}
          onConfirm={() =>
            this.props.onConfirm({ msg: "Confirm Icon Clicked!" })
          }
        >
          {/*closeButton*/}
          <Modal.Header>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>

          <Modal.Body>
          	<Form>
          	  <div className="form-group row">
        <div className="col-md-6">
          <b><label>Company Name</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.companyName}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Email</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.email}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Contact Number</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.contactNumber}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Street Address</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.streetAddress}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Website Url</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.websiteUrl}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Vendor Name</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.vendorName}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Vendor Website Url</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.vendorWebsiteUrl}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Linkedin Url</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.linkedinUrl}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Facebook Url</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.facebookUrl}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Twitter Url</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.twitterUrl}
        </div>
      </div><div className="form-group row">
        <div className="col-md-6">
          <b><label>Pinterest Url</label></b>
        </div>
        <div className="col-md-6">
          {this.props.data.pinterestUrl}
        </div>
      </div>
	
	          {/* <Form.Group controlId="formBasicCheckbox">
	            <Form.Check type="checkbox" label="Check me out" />
	          </Form.Group> */}
	        </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => this.props.onHide({ msg: "Modal Closed!" })}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

class WebsiteInfoTable extends UxCrudTable {
  state = {
	userPermissions: [],
    columns: [],
    itemId: null,
    showView: false,
    showDeleteConfirm: false,
    title: "",
    body: "",
    data: [],
    translations: {
	  titleLabel: "Website Info",
      viewLabel: "View",
      editLabel: "Edit",
      approveLabel: "Approve",
      deleteLabel: "Delete",
      companyNameLabel: "Company Name",
emailLabel: "Email",
contactNumberLabel: "Contact Number",
streetAddressLabel: "Street Address",
websiteUrlLabel: "Website URL",
vendorNameLabel: "Vendor Name",
vendorWebsiteUrlLabel: "Vendor Website URL",
linkedinUrlLabel: "Linkedin URL",
facebookUrlLabel: "Facebook URL",
twitterUrlLabel: "Twitter URL",
pinterestUrlLabel: "Pinterest URL",

    },
  };

 
  async loadTranslations() {
    const thisContext = this;
    let translations = localStorage.getItem("translations");
    if (translations) {
      let translationsObj = JSON.parse(translations);
      thisContext.setState({
        translations: {
          viewLabel: translationsObj["i18n.common.viewLabel"]
            ? translationsObj["i18n.common.viewLabel"]
            : thisContext.state.translations.viewLabel,

          editLabel: translationsObj["i18n.common.editLabel"]
            ? translationsObj["i18n.common.editLabel"]
            : thisContext.state.translations.editLabel,

          approveLabel: translationsObj["i18n.common.approveLabel"]
            ? translationsObj["i18n.common.approveLabel"]
            : thisContext.state.translations.approveLabel,

          deleteLabel: translationsObj["i18n.common.deleteLabel"]
            ? translationsObj["i18n.common.deleteLabel"]
            : thisContext.state.translations.deleteLabel,

          companyNameLabel: translationsObj["i18n.websiteInfo.companyName.label"] ? translationsObj["i18n.websiteInfo.companyName.label"] : thisContext.state.translations.companyNameLabel,
emailLabel: translationsObj["i18n.websiteInfo.email.label"] ? translationsObj["i18n.websiteInfo.email.label"] : thisContext.state.translations.emailLabel,
contactNumberLabel: translationsObj["i18n.websiteInfo.contactNumber.label"] ? translationsObj["i18n.websiteInfo.contactNumber.label"] : thisContext.state.translations.contactNumberLabel,
streetAddressLabel: translationsObj["i18n.websiteInfo.streetAddress.label"] ? translationsObj["i18n.websiteInfo.streetAddress.label"] : thisContext.state.translations.streetAddressLabel,
websiteUrlLabel: translationsObj["i18n.websiteInfo.websiteUrl.label"] ? translationsObj["i18n.websiteInfo.websiteUrl.label"] : thisContext.state.translations.websiteUrlLabel,
vendorNameLabel: translationsObj["i18n.websiteInfo.vendorName.label"] ? translationsObj["i18n.websiteInfo.vendorName.label"] : thisContext.state.translations.vendorNameLabel,
vendorWebsiteUrlLabel: translationsObj["i18n.websiteInfo.vendorWebsiteUrl.label"] ? translationsObj["i18n.websiteInfo.vendorWebsiteUrl.label"] : thisContext.state.translations.vendorWebsiteUrlLabel,
linkedinUrlLabel: translationsObj["i18n.websiteInfo.linkedinUrl.label"] ? translationsObj["i18n.websiteInfo.linkedinUrl.label"] : thisContext.state.translations.linkedinUrlLabel,
facebookUrlLabel: translationsObj["i18n.websiteInfo.facebookUrl.label"] ? translationsObj["i18n.websiteInfo.facebookUrl.label"] : thisContext.state.translations.facebookUrlLabel,
twitterUrlLabel: translationsObj["i18n.websiteInfo.twitterUrl.label"] ? translationsObj["i18n.websiteInfo.twitterUrl.label"] : thisContext.state.translations.twitterUrlLabel,
pinterestUrlLabel: translationsObj["i18n.websiteInfo.pinterestUrl.label"] ? translationsObj["i18n.websiteInfo.pinterestUrl.label"] : thisContext.state.translations.pinterestUrlLabel,

        },
      });
    } else {
      console.log("Alert!!!, Translations not found!");
    }
  }

 async loadColumns() {
    const thisContext = this;

	let columns = [
	    { path: "id", label: "Id" },
	    {
        path: "companyName",
        label: this.state.translations.companyNameLabel,
      },
{
        path: "email",
        label: this.state.translations.emailLabel,
      },
{
        path: "contactNumber",
        label: this.state.translations.contactNumberLabel,
      },
{
        path: "streetAddress",
        label: this.state.translations.streetAddressLabel,
      },
{
        path: "websiteUrl",
        label: this.state.translations.websiteUrlLabel,
      },
{
        path: "vendorName",
        label: this.state.translations.vendorNameLabel,
      },
{
        path: "vendorWebsiteUrl",
        label: this.state.translations.vendorWebsiteUrlLabel,
      },

	    {
	      key: "view",
	      label: this.state.translations.viewLabel,
	      content: (item) => (
	        <WebsiteInfoViewModal
	          itemId={this.state.itemId}
	          show={this.state.showView}
	          title={this.state.title}
	          body={this.state.body}
	          data={this.state.data}
	          onClick={() => this.handleViewShow(item)}
	          onHide={this.handleViewClose}
	        />
	      ),
	    },
  	];

	if (thisContext.state.userPermissions.includes("WEBSITE_INFO_UPDATE")) {
      columns.push({
        key: "edit",
        label: this.state.translations.editLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onEdit(item)}
            className="btn btn-info"
          >
            <FontAwesomeIcon icon={faPencilAlt} />
          </button>
        ),
      });
    }

    if (thisContext.state.userPermissions.includes("WEBSITE_INFO_APPROVE")) {
      columns.push({
        key: "approve",
        label: this.state.translations.approveLabel,
        content: (item) => (
          <button
            onClick={() => this.props.onApprove(item)}
            className="btn btn-success"
            disabled={item.status == "APPROVED"}
          >
            <FontAwesomeIcon icon={faCheck} />
          </button>
        ),
      });
    }

    if (thisContext.state.userPermissions.includes("WEBSITE_INFO_DELETE")) {
      columns.push({
        key: "delete",
        label: this.state.translations.deleteLabel,
        content: (item) => (
          <DeleteConfirmModal
            disabled={item.status == "APPROVED"}
            show={this.state.showDeleteConfirm}
            item={this.state.item}
            onClick={() => this.handleDeleteModalShow(item)}
            onHide={this.handleDeleteConfirmClose}
            onConfirm={(item) => this.handleDeleteFromModal(item)}
          />
        ),
      });
    }

	this.setState({ columns: columns });
  }

}

export default WebsiteInfoTable;
