import React from "react";
import { Accordion, Nav, Collapse } from "react-bootstrap";
import { Link, NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown, faCaretUp } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";
import {
  faHome,
  faBriefcase,
  faPaperPlane,
  faQuestion,
  faCopy,
  faImage,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";

class SubMenuSettings extends React.Component {
  constructor(props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
    };
  }

  toggleNavbar() {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  render() {
    const { icon, title, items, translations, userPermissions } = this.props;

    return (
      <Nav.Item className={classNames({ open: !this.state.collapsed })}>
        <Accordion>
          <Accordion.Toggle
            as={Nav.Link}
            variant="link"
            eventKey="0"
            onClick={this.toggleNavbar}
          >
            <FontAwesomeIcon icon={icon} className="mr-2" />
            {title}
            <FontAwesomeIcon
              icon={this.state.collapsed ? faCaretDown : faCaretUp}
              className="float-right"
            />
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="0">
            <nav className="nav flex-column">
              {/* <Nav.Item>
              <Nav.Link as={"div"}>
                <NavLink to={"/admin/userRequests"} className="nav-link nav-item">
                  <FontAwesomeIcon icon={faCopy} className="mr-2" />
                  User Request
                </NavLink>
                </Nav.Link>
              </Nav.Item> */}

              {userPermissions.includes("PRODUCT_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/products" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Product
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}
              {userPermissions.includes("SUB_CATEGORY_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/subCategorys" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Sub Category
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}
              {userPermissions.includes("SUPPLIER_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/suppliers" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Supplier
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}


              {userPermissions.includes("CATEGORY_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/categorys" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Category
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

              {userPermissions.includes("PRODUCT_VARIANT_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/productVariants" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Product Variant
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

              {userPermissions.includes("VARIANT_VALUES_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/variantValuess" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Variant Values
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

              {userPermissions.includes("PRODUCT_PRICE_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/productPrices" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Product Price
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

              {userPermissions.includes("PRODUCT_RATING_READ") && (
                <Nav.Item>
                  <Nav.Link>
                    <NavLink to="/admin/productRatings" className="nav-link nav-item">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Product Rating
                    </NavLink>
                  </Nav.Link>
                </Nav.Item>
              )}

            </nav>
          </Accordion.Collapse>
        </Accordion>
      </Nav.Item>
    );
  }
}

export default SubMenuSettings;
